import { onMounted, onBeforeUnmount } from 'vue';

export function useGlobalDigitListener(callback) {
    const handleKeydown = (e) => {
        const activeEl = document.activeElement;
        const isInputFocused = activeEl && (
            activeEl.tagName === 'INPUT' ||
            activeEl.tagName === 'TEXTAREA' ||
            activeEl.isContentEditable
        );

        if (isInputFocused) {
            return;
        }

        // ÐŸÑ€Ð¾Ð²ÐµÑ€ÑÐµÐ¼, Ñ‡Ñ‚Ð¾ Ð½Ð° ÑÑ‚Ñ€Ð°Ð½Ð¸Ñ†Ðµ Ð½ÐµÑ‚ modal-box-background Ð¸Ð»Ð¸ dp__outer_menu_wrap
        if (
            document.querySelector('.modal-box-background') ||
            document.querySelector('.dp__outer_menu_wrap')
        ) {
            return;
        }

        // ÐŸÑ€Ð¾Ð²ÐµÑ€ÑÐµÐ¼, Ñ‡Ñ‚Ð¾ Ð½Ð°Ð¶Ð°Ñ‚Ð° Ñ†Ð¸Ñ„Ñ€Ð° (0â€“9)
        if (e.key >= '0' && e.key <= '9') {
            callback(e.key)
        }
    }

    onMounted(() => {
        window.addEventListener('keydown', handleKeydown)
    });

    onBeforeUnmount(() => {
        window.removeEventListener('keydown', handleKeydown)
    });
}
