<template>
    <!-- Instrumental -->
    <FormLine :field="fields.instrumental" :column="true" :viewMode="props.viewMode">
        <template v-slot:title>
            <Toggler
                :name="fields.instrumental.name"
                :inline="true"
                :disabled="!props.permissions.canEdit"
                :checked="data.instrumental"
                class="reverse solid"
                @onClick="onInstrumentalChange"
            />
        </template>
    </FormLine>

    <!-- Explicit -->
    <FormLine :field="fields.isExplicit" :column="true" v-if="!data.instrumental" :viewMode="props.viewMode">
        <template v-slot:title>
            <Toggler
                :name="fields.isExplicit.name"
                :inline="true"
                :disabled="!props.permissions.canEdit"
                :checked="data.isExplicit"
                class="reverse solid"
                @onClick="onIsExplicitChange"
            />
        </template>
    </FormLine>

    <!-- Language -->
    <FormLine :field="fields.language" v-if="!data.instrumental" :viewMode="props.viewMode">
        <AutoComplete
            class="noerror light"
            :optionList="data.languageOptions"
            :placeholder="t(`pages.tracks.fields.language.placeholder`)"
            :disabled="!props.permissions.canEdit"
            :values="data.language ? [data.language] : []"
            :leftAuto="false"
            :maxValuesCount="1"
            :rightAuto="false"
            @search="onLanguageSearch"
            @change="onLanguageChange"
        />
    </FormLine>

    <!-- Lyrics -->
    <FormLine :field="fields.lyrics" v-if="!data.instrumental" :viewMode="props.viewMode">
        <Textarea
            :placeholder="t(`pages.tracks.fields.lyrics.placeholder`)"
            :value="data.lyrics"
            :disabled="!props.permissions.canEdit"
            class="noerror light with-placeholder"
            :class="{ preview: props.viewMode === 'table' }"
            @change="onLyricsChange"
        />
    </FormLine>
</template>

<script setup>
    import { reactive } from 'vue';
    import { useI18n } from 'vue-i18n';
    
    import Toggler from 'ðŸ”—/components/ui/Toggler.vue';
    import AutoComplete from 'ðŸ”—/components/ui/AutoComplete.vue';
    import Textarea from 'ðŸ”—/components/ui/Textarea.vue';
    import FormLine from '@/components/ui/FormLine.vue';
    import { ALL_LANGUAGES_LIST } from 'ðŸ”—/constants';

    const emit = defineEmits(['change', 'error', 'metadata', 'save']);
    const { t } = useI18n();

    const props = defineProps({
        permissions: { type: Object, default: () => ({}) },
        item: { type: [Object, null], default: null },
        viewMode: { type: String, default: 'list' }
    });

    const fields = {
        instrumental: {
            name: t(`pages.tracks.fields.instrumental.name`),
            attentions: [
                t(`pages.tracks.fields.instrumental.attention`)
            ],
            width: 130
        },
        isExplicit: {
            name: t(`pages.tracks.fields.is-explicit.name`),
            attentions: [
                t(`pages.tracks.fields.is-explicit.attention`)
            ],
            width: 110
        },
        language: {
            name: t(`pages.tracks.fields.language.name`),
            width: 240
        },
        lyrics: {
            name: t(`pages.tracks.fields.lyrics.name`),
            width: 240
        }
    };

    emit('metadata', {
        fields,
        direction: 'row'
    });

    const data = reactive({
        instrumental: props.item?.instrumental,
        isExplicit: props.item?.isExplicit,
        language: props.item?.language ? ALL_LANGUAGES_LIST.find(item => item.code === props.item?.language) : null,
        lyrics: props.item?.lyrics,

        languageOptions: ALL_LANGUAGES_LIST
    });

    const onInstrumentalChange = (e) => {
        data.instrumental = !!e?.value;

        const fields = ['instrumental'];

        if (data.instrumental) {
            if (data.language) {
                data.language = null;
                fields.push('language');
            }

            if (data.isExplicit) {
                data.isExplicit = false;
                fields.push('isExplicit');
            }

            if (data.lyrics) {
                data.lyrics = null;
                fields.push('lyrics');
            }
        }

        const payload = {};
        fields.forEach(field => payload[field] = data[field]);

        emit('change', payload);
    }

    const onIsExplicitChange = (e) => {
        data.isExplicit = !!e?.value;

        emit('change', { isExplicit: data.isExplicit });
    }

    const onLanguageSearch = (value) => {
        const searchStr = value?.toLowerCase().trim();

        if (!searchStr) {
            data.languageOptions = ALL_LANGUAGES_LIST;
            return    
        }

        data.languageOptions = ALL_LANGUAGES_LIST.filter(item =>
            item.name.toLowerCase().indexOf(searchStr) !== -1);
    }

    const onLanguageChange = (e) => {
        if (!e?.values) {
            return;
        }

        if (e.values?.length) {
            data.language = e?.values[0];
        } else {
            data.language = null;
        }

        emit('change', { language: data.language?.code });
    }

    const onLyricsChange = (e) => {
        data.lyrics = e.target.value?.trim();
        emit('change', { lyrics: data.lyrics });
    }
</script>

<style lang="scss" scoped>
</style>
