    import SectionProjectInfo from './SectionProjectInfo.vue';
    import SectionRating from './SectionRating.vue';
    import SectionSnippet from './SectionSnippet.vue';
    import SectionRightFlags from './SectionRightFlags.vue';
    import SectionLyricsLanguage from './SectionLyricsLanguage.vue';
    import SectionIdentifiersRelease from './SectionIdentifiersRelease.vue';
    import SectionMetadata from './SectionMetadata.vue';

