<template>
    <div class="items-editor-header">
        <div v-for="(item, id) in items"
            :key="item.id"
            :ref="el => (sectionRefs[id] = el)"
            :id="item.id"
            :style="{
                width: item.width + 'px',
                'max-width': item.width + 'px',
                'min-width': item.width + 'px'
            }"
            class="section"
        >
            <div class="title">
                <label v-if="item.name">
                    {{ t(item.name) }}

                    <Icon icon="help"
                        v-if="item.metadata?.help?.length"
                        class="help"
                        v-tooltip="item.metadata.help.map(item => `<b>${item.title}</b><br/>${item.description}`).join('<br/><br/>')"/>

                </label>
            </div>
            <div class="fields" v-if="item.fields">
                <div v-for="field in item.fields"
                    :key="field.id"
                    :style="{
                        width: field.width + 'px',
                        'max-width': field.width + 'px',
                        'min-width': field.width + 'px'
                    }"
                >
                    <label>
                        {{ field.name }}
                        <Icon icon="help"
                            v-if="field.attentions?.length"
                            class="help"
                            v-tooltip="field.attentions.join('<br/>')"/>

                    </label>
                    <span v-if="field.attention">
                        {{ field.attention }}
                    </span>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
    import { useI18n } from 'vue-i18n';
    import { computed, ref } from 'vue';
    import { useGlobalDigitListener } from 'ðŸ”—/utils/hooks/keydown'
    import stores from '@/stores';

    const emit = defineEmits(['change']);
    const locale = stores.locale();

    const { t } = useI18n();
    const sectionRefs = ref([]);

    const props = defineProps({
        sections: { type: Array, default: () => ([]) }
    });

    const items = computed(() => {
        return [
            {
                id: 'preview',
                width: 324
            },
            ...props.sections.map(item => ({
                ...item,
                fields: Object.keys(item.metadata.fields).map(id => ({
                        id,
                        ...item.metadata.fields[id],
                        width: item.metadata.fields[id].width || 200
                    })),
                width: item.metadata?.direction === 'row' ? 
                    Object.values(item.metadata.fields).reduce((acc, field) => acc + (acc, field.width || 200), 0) : 
                    Math.max(...Object.values(item.metadata.fields).map(field => field.width || 200))
            })),
            {
                id: 'actions',
                width: 140
            }
        ]
    });

    const scrollToSection = (id) => {
        const el = sectionRefs.value.find((e) => e?.id === id);
        const container = sectionRefs.value[0]?.closest('.box.table');

        if (el && container) {
            const offsetLeft = el.offsetLeft;
            const elementWidth = el.offsetWidth;
            const containerClientWidth = container.clientWidth;
            const scrollWidth = container.scrollWidth;
            const extraOffset = 300;

            if (locale.rtl) {
                // Ð Ð°ÑÑÑ‚Ð¾ÑÐ½Ð¸Ðµ Ð¾Ñ‚ Ð¿Ñ€Ð°Ð²Ð¾Ð³Ð¾ ÐºÑ€Ð°Ñ ÐºÐ¾Ð½Ñ‚ÐµÐ¹Ð½ÐµÑ€Ð° Ð´Ð¾ Ð¿Ñ€Ð°Ð²Ð¾Ð³Ð¾ ÐºÑ€Ð°Ñ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð°
                const elementRightOffset = scrollWidth - offsetLeft - elementWidth;
                // ÐŸÑ€Ð¸ Ð¾Ñ‚Ñ€Ð¸Ñ†Ð°Ñ‚ÐµÐ»ÑŒÐ½Ð¾Ð¼ scrollLeft Ñ†ÐµÐ»ÐµÐ²Ð¾Ðµ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ = -elementRightOffset + extraOffset
                const targetScrollLeft = -(elementRightOffset - extraOffset);

                container.scrollTo({
                    left: targetScrollLeft,
                    behavior: 'smooth'
                });
            } else {
                container.scrollTo({
                    left: offsetLeft - extraOffset,
                    behavior: 'smooth'
                });
            }
        }
    };

    useGlobalDigitListener((digit) => {
        if (items.value[digit ? digit : 10]) {
            scrollToSection(items.value[digit ? digit : 10].id);
        }
    });
</script>

<style lang="scss" scoped>
    .items-editor-header {
        display: flex;
        flex-direction: row;
        align-content: start;
        overflow: visible;
        position: sticky;
        top: 0;
        background: var(--white-500);
        z-index: 2;
        box-shadow: 0px 8px 8px -12px var(--dark-500);
        width: max-content;
        min-width: 100%;

        .section {
            display: flex;
            flex-direction: column;
            gap: 4px;
            .fields {
                display: flex;
                flex-direction: row;

                & > div {
                    padding: 4px 8px;
                    display: flex;
                    flex-direction: column;
                    gap: 4px;
                    label {
                        font: var(--font-body-1);
                        color: var(--dark-500);
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        gap: 8px;
                        svg {
                            // margin-right: auto;
                        }
                    }
                    & > span {
                        font: var(--font-caption);
                        color: var(--dark-80);
                    }
                }
            }

            .title {
                padding: 4px 8px;
                display: flex;
                flex-direction: row;
                margin: 0 auto;
                min-height: 42px;

                label {
                    font: var(--font-h5);
                    font-weight: bold;
                    color: var(--dark-500);
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    gap: 8px;
                    svg {
                        // margin-right: auto;
                    }
                }
            }

            .help {
                @include studio.help-icon;
            }

            &:nth-child(2n) {
                background: var(--light-10);
            }

            &:first-child {
                position: sticky;
                left: 0;
                background: var(--white-500);
                box-shadow: 8px 0px 8px -12px var(--dark-500);

                &:after {
                    content: "";
                    left: 0;
                    right: 0;
                    height: 30px;
                    bottom: -8px;
                    display: flex;
                    position: absolute;
                    pointer-events: none;
                    z-index: -1;
                    background: var(--white-500);
                }
            }
            &:last-child {
                position: sticky;
                right: 0;
                margin-left: auto;
                // background: var(--white-500);
                    // box-shadow: -8px 0px 8px -12px var(--dark-500);

                &:before {
                    content: "";
                    width: 120px;
                    height: 30px;
                    display: block;
                    background: var(--primary-500);
                    border-top-left-radius: 24px;
                    position: absolute;
                    left: 0px;
                    bottom: -8px;
                    z-index: -1;
                }

                &:after {
                    content: "";
                    left: 64px;
                    right: 0;
                    height: 30px;
                    bottom: -8px;
                    display: flex;
                    background: var(--primary-500);
                    position: absolute;
                    pointer-events: none;
                    z-index: -1;
                }
            }
        }
    }


    .direction-rtl .items-editor-header {
        .section {
            &:first-child {
                left: auto;
                right: 0;
                box-shadow: -8px 0px 8px -12px var(--dark-500);
            }
                
            &:last-child {
                right: auto;
                left: 0;
                &:before {
                    left: auto;
                    right: 0;
                    border-top-left-radius: 0;
                    border-top-right-radius: 24px;
                }
                &:after {
                    left: 0;
                    right: 64px;
                }
            }
        }
    }
</style>