<template>
    <div class="section-menu" v-if="props.viewMode === 'list' && props.item.id && sections?.length > 1">
        <template v-for="(item, id) in props.sections" :key="item.id">
            <Button
                class="size-s tertiary"
                :name="t(item.title)"
                :ref="el => (menuRefs[id] = el)"
                :class="{ submenu: data.activeSection !== item.id }"
                @click="scrollToSection(item.id)"
            />
        </template>
        <Icon icon="help" v-tooltip="t(props.help)" v-if="props.help" class="help"/>
    </div>

    <div v-for="(item, id) in props.sections" 
        :key="item.id"
        :id="item.id + ( props.viewMode === 'table' ? `-${ props.item?.localId || props.item?.id}`: '')"
        :ref="el => (sectionRefs[id] = el)"
        :style="{
            width: props.viewMode === 'table' ? item.width + 'px' : null,
            'max-width': props.viewMode === 'table' ? item.width + 'px' : null,
            'min-width': props.viewMode === 'table' ? item.width + 'px' : null
        }"
        class="section"
        :class="{
            'view-mode-table': props.viewMode === 'table',
            'temporary-hidden': props.temporaryHidden && !!id && !props.item.id
        }"
    >
        <div class="form-line column" v-if="item.name && props.viewMode === 'list'">
            <label class="title">{{ t(item.name) }}</label>
        </div>

        <div :class="{
            row: item.metadata?.direction === 'row',
            column: item.metadata?.direction === 'column'
        }">
            <component :is="item.component"
                :item="props.item"
                :permissions="props.permissions"
                :viewMode="props.viewMode"
                @change="emit('change', $event)"
                @error="emit('error', $event)"
                @save="emit('save', $event)"
                @metadata="setMetadata(item, $event)"
            ></component>
        </div>
        
        <div class="form-line divider" v-if="props.viewMode === 'list' && id < props.sections.length - 1 && props.item.id"></div>
    </div>
</template>

<script setup>
    import { onMounted, onBeforeUnmount, ref, reactive } from 'vue';
    import { useI18n } from 'vue-i18n';
    
    import Button from 'ðŸ”—/components/ui/Button.vue';

    import { useGlobalDigitListener } from 'ðŸ”—/utils/hooks/keydown'


    const emit = defineEmits(['change', 'error', 'sections', 'save']);
    const { t } = useI18n();

    const sectionRefs = ref([]);
    const menuRefs = ref([]);

    const props = defineProps({
        permissions: { type: Object, default: () => ({}) },
        item: { type: [Object, null], default: null },
        sections: { type: Array, default: () => ([]) },
        viewMode: { type: String, default: 'list' },
        help: { type: [String, null], default: null },
        temporaryHidden: { type: Boolean, default: false }
    });

    const data = reactive({});

    const setMetadata = (section, payload) => {
        section.metadata = payload;

        section.width = section.metadata?.direction === 'row' ? 
            Object.values(section.metadata.fields).reduce((acc, field) => acc + (acc, field.width || 200), 0) : 
            Math.max(...Object.values(section.metadata.fields).map(field => field.width || 200))

        if (props.sections.every(item => !!item.metadata)) {
            emit('sections', props.sections)
        }
    }

    const scrollToSection = (id) => {
        const el = sectionRefs.value.find((e) => e?.id === id);
        const container = sectionRefs.value[0].closest('.item-detail');

        if (el && container) {
            const offsetTop = el.offsetTop;
            container.scrollTo({
                top: offsetTop - 120,
                behavior: 'smooth'
            });
        }
    };

    const onScroll = (e) => {
        const scrollContainer = sectionRefs.value[0].closest('.item-detail');
        const scrollTop = scrollContainer.scrollTop;
        const containerTop = scrollContainer.getBoundingClientRect().top;

        let bestMatch = null;
        let bestOffset = Infinity;

        sectionRefs.value.forEach((sectionEl) => {
            if (!sectionEl) {
                return;
            }

            const offset = Math.abs(sectionEl.getBoundingClientRect().top - containerTop - 60);

            if (offset < bestOffset) {
                bestOffset = offset;
                bestMatch = sectionEl.id;
            }
        });

        data.activeSection = bestMatch;
    };

    useGlobalDigitListener((digit) => {
        if (props.viewMode !== 'list') {
            return;
        }

        if (props.sections[digit ? digit - 1 : 9]) {
            scrollToSection(props.sections[digit ? digit - 1 : 9].id);
        }
    });

    onBeforeUnmount(() => {
        const container = sectionRefs.value[0].closest('.item-detail');
        container.removeEventListener('scroll', onScroll);
    });

    onMounted(() => {
        const container = sectionRefs.value[0].closest('.item-detail');
        container.addEventListener('scroll', onScroll);

        onScroll();
    });
</script>

<style lang="scss" scoped>
    .section {
        display: flex;
        flex-direction: column;
        gap: 24px;

        & > div {
            display: flex;
            flex-direction: column;
            gap: 24px;
        }

        &:last-child {
            margin-bottom: 52px;
        }

        &.temporary-hidden {
            display: none;
        }

        &.view-mode-table {
            flex-direction: row;
            gap: 0;
            flex-shrink: 0;

            & > .row {
                display: flex;
                flex-direction: row;
                gap: 0;
            }

            & > .column {
                display: flex;
                flex-direction: column;
                gap: 8px;
            }

            &.temporary-hidden {
                display: flex;
                opacity: 0;
                pointer-events: none;
            }

            &:nth-child(2n + 1) {
                background: var(--light-20);
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    .form-line {
        @include studio.form-line();
    }

    .section-menu {
        display: flex;
        flex-direction: row;
        align-items: center;
        flex-wrap: wrap;
        gap: 16px;
        position: sticky;
        top: -36px;
        background: var(--white-500);
        padding: 8px 0;
        z-index: 1;
        button {
            padding: 0;
            min-width: 0;
        }

        .help {
            @include studio.help-icon;
        }
    }
</style>
