<template>
    <div class="track-info"
        v-if="!data.loading"
        v-show="!data.updating || props.viewMode === 'table'"
        :class="{
            'view-mode-table': props.viewMode === 'table',
            'updating': data.updating
        }"
        ref="trackInfoBox"
    >
        <label v-if="props.viewMode === 'list'"
            class="title">{{ t(`pages.tracks.${
                props.track?.id ? (!canEdit ? 'view' : 'edit') : 'add'
            }.title`) }}</label>

        <Sections :item="data.item"
            :viewMode="props.viewMode"
            :permissions="{
                canEdit,
                canEditRating,
                canEditContributor
            }"
            @change="onSectionChange"
            @error="onSectionError"
            @save="saveChanges"
            @sections="emit('sections', $event)"
        />


        <div :class="props.viewMode === 'table' ? 'actions-box detail column' : ''">
            <Teleport :to="`#${props.toolbarId}`" :disabled="props.viewMode !== 'list'">
                <span class="devider" v-if="props.viewMode === 'list'" />

                <KebabMenu
                    v-if="data.id && props.viewMode === 'list'"
                    :leftAuto="!locale.rtl"
                    :rightAuto="locale.rtl"
                    :tertiary="true"
                    :invert="true"
                    size="size-s"
                >
                    <ul>
                        <li v-for="item in editableReleases" :key="item.id"
                            @click="removeItemFromRelease(item)">
                            <Img v-if="item.cover?.resized || defaultReleaseCover?.resized"
                                :preview="item.cover?.resized || defaultReleaseCover?.resized"
                                :original="item.cover?.original || defaultReleaseCover?.original"
                                :alt="item.title"
                                :skeleton="true"
                            />
                            <span v-html="t(`pages.tracks.remove-from-release.title`, { title: item.title })"></span>
                        </li>

                        <li v-for="item in editablePlaylists" :key="item.id"
                            @click="removeItemFromPlaylist(item)">
                            <Img v-if="item.cover?.resized || defaultPlaylistCover?.resized"
                                :preview="item.cover?.resized || defaultPlaylistCover?.resized"
                                :original="item.cover?.original || defaultPlaylistCover?.original"
                                :alt="item.title"
                                :skeleton="true"
                            />
                            <span v-html="t(`pages.tracks.remove-from-playlist.title`, { title: item.title })"></span>
                        </li>

                        <li @click="remove" v-if="canRemove">
                            <Icon icon="trash" />
                            <span>{{ t(`pages.tracks.remove.title`) }}</span>
                        </li>
                        <li @click="close">
                            <Icon icon="close" />
                            <span>{{ t(`pages.tracks.cancel`) }}</span>
                        </li>
                    </ul>
                </KebabMenu>

                <template v-if="(canRemove || !!editableReleases?.length || !!editablePlaylists?.length) && props.viewMode === 'table' && data.id"
                >
                    <Button :title="t(`pages.tracks.remove.title`)"
                        class="size-m tertiary invert"
                        icon="trash"
                        :disabled="!canRemove"
                        v-if="!editableReleases?.length && !editablePlaylists?.length"
                        @click="remove"
                    />

                    <KebabMenu
                        v-else
                        :leftAuto="!locale.rtl"
                        :rightAuto="locale.rtl"
                        :tertiary="true"
                        :invert="true"
                        icon="trash"
                        size="size-s"
                    >
                        <ul>
                            <li v-for="item in editableReleases" :key="item.id"
                                @click="removeItemFromRelease(item)">
                                <Img v-if="item.cover?.resized || defaultReleaseCover?.resized"
                                    :preview="item.cover?.resized || defaultReleaseCover?.resized"
                                    :original="item.cover?.original || defaultReleaseCover?.original"
                                    :alt="item.title"
                                    :skeleton="true"
                                />
                                <span v-html="t(`pages.tracks.remove-from-release.title`, { title: item.title })"></span>
                            </li>

                            <li v-for="item in editablePlaylists" :key="item.id"
                                @click="removeItemFromPlaylist(item)">
                                <Img v-if="item.cover?.resized || defaultPlaylistCover?.resized"
                                    :preview="item.cover?.resized || defaultPlaylistCover?.resized"
                                    :original="item.cover?.original || defaultPlaylistCover?.original"
                                    :alt="item.title"
                                    :skeleton="true"
                                />
                                <span v-html="t(`pages.tracks.remove-from-playlist.title`, { title: item.title })"></span>
                            </li>

                            <li @click="remove">
                                <Icon icon="trash" />
                                <span>{{ t(`pages.tracks.remove.title`) }}</span>
                            </li>
                        </ul>
                    </KebabMenu>
                </template>

                <StateKebabMenu v-if="data.id"
                    :state="data.item?.state"
                    :states="data.states"
                    :disabled="!canEditState"
                    @change="changeState"
                />

                <Button :name="t(`pages.tracks.${ props.track?.id ? 'edit' : 'add' }.save-and-add-more`)"
                    class="size-s secondary"
                    :disabled="saveDisabled"
                    v-if="(canEdit || canEditContributor) && props.viewMode === 'list'"
                    @click="saveChangesAndAddMore"
                />

                <Button :name="t(`pages.tracks.${ props.track?.id ? 'edit' : 'add' }.save`)"
                    class="size-s"
                    :disabled="saveDisabled"
                    v-if="(canEdit || canEditContributor)"
                    :loading="data.uploading"
                    :errorsTooltip="errorsTooltip"
                    :class="{
                        tertiary: data.uploading,
                        active: data.uploading,
                        invert: data.uploading,
                        secondary: !data.uploading
                    }"
                    @click="saveChanges"
                />
            </Teleport>
        </div>
    </div>
    <div class="loader" v-if="data.loading || (data.updating && props.viewMode !== 'table')">
        <div>
            <Loader :inside="true" class="tertiary" />
        </div>
    </div>
</template>

<script setup>
    import { reactive, computed, onMounted, ref, watch } from 'vue';

    import Sections from './sections/Sections.vue';

    import { useI18n } from 'vue-i18n';
    import api from 'ðŸ”—/api';
    import Img from 'ðŸ”—/components/ui/Img.vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import StateKebabMenu from 'ðŸ”—/components/ui/StateKebabMenu.vue';
    import KebabMenu from 'ðŸ”—/components/ui/KebabMenu.vue';
    import Loader from 'ðŸ”—/components/ui/Loader.vue';
    import { delay, message } from 'ðŸ”—/utils/utils';
    import stores from '@/stores';
    import { code } from 'ðŸ”—/utils/utils';

    const modals = stores.modals();

    const releases = api.releases();
    const playlists = api.playlists();
    const tracks = api.tracks();
    const user = stores.user();
    const locale = stores.locale();

    const emit = defineEmits(['update', 'add', 'remove', 'edit', 'sections', 'close', 'hide', 'actions']);
    const { t } = useI18n();

    const trackInfoBox = ref(null);

    const props = defineProps({
        toolbarId: { type: String, default: '' },
        track: { type: [Object, null], default: null },
        artist: { type: [Object, null], default: null },
        release: { type: [Object, null], default: null },
        playlist: { type: [Object, null], default: null },
        position: { type: [Number, null], default: null },
        viewMode: { type: String, default: 'list' }
    });

    // Permissions
        const canEdit = computed(() => {
            if (data.item?.id) {
                return tracks.canEdit({
                    track: data.item
                });
            }

            return tracks.canAdd({
                artist: props.artist
            });
        });

        const canRemove = computed(() => {
            return tracks.canRemove({
                track: data.item
            });
        });

        const canEditRating = computed(() => {
            return tracks.canEditRating({
                track: data.item
            });
        });

        const canEditState = computed(() => {
            return tracks.canEditState({
                track: data.item
            });
        });

        const canEditContributor = computed(() => {
            if (data.item?.id) {
                return tracks.canEditContributor({
                    track: data.item
                });
            }

            return tracks.canAdd({
                artist: props.artist
            });
        });


    const data = reactive({
        id: null,
        item: {
            instrumental: true
        },
        updatedItem: {},
        errors: {},
        states: [],
        loading: true,
        uploading: false,

        releases: [], // Ð ÐµÐ»Ð¸Ð·Ñ‹, Ð² ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ñ… ÐµÑÑ‚ÑŒ ÑÑ‚Ð¾Ñ‚ Ñ‚Ñ€ÐµÐº
        playlists: [], // ÐŸÐ»ÐµÐ¹Ð»Ð¸ÑÑ‚Ñ‹, Ð² ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ñ… ÐµÑÑ‚ÑŒ ÑÑ‚Ð¾Ñ‚ Ñ‚Ñ€ÐµÐº. Ð¡ÐºÐ¾Ñ€ÐµÐµ Ð²ÑÐµÐ³Ð¾ Ð±ÑƒÐ´ÐµÑ‚ Ñ‚ÑƒÑ‚ Ñ‚Ð¾Ð»ÑŒÐºÐ¾ Ñ‚Ð¾Ñ‚, Ñ‡Ñ‚Ð¾ Ð¿ÐµÑ€ÐµÐ´Ð°Ð»Ð¸ Ð² Ð¿Ñ€Ð¾Ð¿ÑÐ°Ñ…, Ñ‡Ñ‚Ð¾Ð±Ñ‹ Ð½Ðµ Ð¿ÐµÑ€ÐµÐ·Ð°Ð³Ñ€ÑƒÐ¶Ð°Ñ‚ÑŒ Ð¸Ð½Ñ‚ÐµÑ€Ñ„ÐµÐ¹Ñ, Ð¸Ð±Ð¾ Ð¿Ð»ÐµÐ¹Ð»Ð¸ÑÑ‚Ð¾Ð² Ð¼Ð¾Ð³ÑƒÑ‚ Ð±Ñ‹Ñ‚ÑŒ ÑÐ¾Ñ‚Ð½Ð¸ Ñ‚Ñ‹ÑÑÑ‡ Ñƒ Ñ‚Ñ€ÐµÐºÐ°
    });


    // Ð ÐµÐ»Ð¸Ð·Ñ‹, ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ðµ ÐµÑ‰Ðµ Ð½Ðµ Ð¾Ð¿ÑƒÐ±Ð»Ð¸ÐºÐ¾Ð²Ð°Ð½Ñ‹, Ð° Ð·Ð½Ð°Ñ‡Ð¸Ñ‚ ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ðµ Ð¼Ñ‹ Ð¼Ð¾Ð¶ÐµÐ¼ Ñ€ÐµÐ´Ð°ÐºÑ‚Ð¸Ñ€Ð¾Ð²Ð°Ñ‚ÑŒ
    const editableReleases = computed(() => {
        return data.releases?.filter(item => releases.canAddTrack({
            release: item,
            artist: props.artist,
            track: data.item
        })) || [];
    });

    // ÐŸÐ»ÐµÐ¹Ð»Ð¸ÑÑ‚Ñ‹, ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ðµ ÐµÑ‰Ðµ Ð½Ðµ Ð¾Ð¿ÑƒÐ±Ð»Ð¸ÐºÐ¾Ð²Ð°Ð½Ñ‹, Ð° Ð·Ð½Ð°Ñ‡Ð¸Ñ‚ ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ðµ Ð¼Ñ‹ Ð¼Ð¾Ð¶ÐµÐ¼ Ñ€ÐµÐ´Ð°ÐºÑ‚Ð¸Ñ€Ð¾Ð²Ð°Ñ‚ÑŒ
    const editablePlaylists = computed(() => {
        return data.playlists?.filter(item => playlists.canAddTrack({
            playlist: item,
            artist: props.artist,
            track: data.item
        })) || [];
    });

    const defaultReleaseCover = computed(() => {
        return api.releases().defaultCover;
    });

    const defaultPlaylistCover = computed(() => {
        return api.playlists().defaultCover;
    });

    const getStates = async () => {
        data.states = data.id ? await tracks.getAvailableStates(data.id) || [] : [];
    }

    const changeState = async (state) => {
        modals.confirm({
            title: t(`pages.tracks.state.confirm.title`),
            body: t(`pages.tracks.state.confirm.description`, {
                name: t(`state.default.code.${ state.toLowerCase() }`)
            }),
            onOk: async () => {
                const payload = await tracks.changeState(data.id, state);

                data.item.state = payload?.state;
                getStates();

                emit('update', data.item);

                modals.open('success', {
                    data: {
                        title: t(`pages.tracks.state.done.title`),
                        close: t(`pages.tracks.state.done.close`)
                    },
                    quietClose: true
                });
            }
        }); 
    }

    const onSectionError = (payload) => {
        if (!payload || !Object.keys(payload)?.length) {
            return;
        }

        Object.keys(payload).forEach(field => {
            if (payload[field]?.length) {
                data.errors[field] = payload[field];
            } else {
                delete data.errors[field];
            }
        });
    }

    const onSectionChange = (payload) => {
        // Ð’Ñ‹Ñ‚Ð°ÑÐºÐ¸Ð²Ð°ÐµÐ¼ Ñ‚Ñ€ÐµÐº Ñ„Ð°Ð¹Ð» Ð¸Ð· Ð¿Ð¾Ñ‚Ð¾ÐºÐ° Ð¸ ÑÐ¾Ñ…Ñ€Ð°Ð½ÑÐµÐ¼ Ð½Ð°Ð»ÐµÑ‚Ñƒ
        if (data.id && payload?.trackFile?.file) {
            updateTrackFile(data.id, payload.trackFile.file, payload.trackFile.callback);
            delete payload.trackFile;
        }

        if (!payload || !Object.keys(payload)?.length) {
            return;
        }

        Object.keys(payload).forEach(field => data.updatedItem[field] = payload[field]);

        emit('edit', {
            ...data.item,
            ...data.updatedItem
        });
    }

    const hasChanges = computed(() => {
        if (!data.updatedItem) {
            return false;
        }

        return !!Object.keys(data.updatedItem)?.length;
    });

    const saveDisabled = computed(() => {
        return (!data.updatedItem?.title?.trim().length && !data.item?.title?.trim().length) ||
            (typeof(data.updatedItem?.title) === 'string' && !data.updatedItem.title.trim().length) ||
            !!Object.keys(data.errors)?.length || data.uploading || !hasChanges.value;
    });

    const errorsTooltip = computed(() => {
        const errors = [];
        Object.values(data.errors)?.forEach(item => errors.splice(0, 0, ...item));
        return errors.join('<br>');
    });


    const close = () => {
        emit('close', data.item);
    }

    const remove = async () => {
        if (!data.item?.id) {
            return;
        }

        modals.confirm({
            title: t(`pages.tracks.remove.confirm.title`),
            body: t(`pages.tracks.remove.confirm.description`, { title: data.item?.title }),
            onOk: async () => {
                await tracks.remove(data.id);

                modals.open('success', {
                    data: {
                        title: t(`pages.tracks.remove.done.title`),
                        close: t(`pages.tracks.remove.done.close`)
                    },
                    quietClose: true
                });

                emit('remove', data.item.id);
            }
        }); 
    };

    const updateTrackFile = async (id, file, callback) => {
        if (!id || !file) {
            callback();
            return;
        }

        data.uploading = true;

        const payload = await tracks.updateAudioFile(id, file);

        if (!payload?.trackFile) {
            callback();
            return;
        }

        data.item = { ...data.item, ...{
            bpm: payload.bpm,
            duration: payload.duration,
            tempos: payload.tempos,
            trackFile: payload.trackFile,
            wave: payload.wave
        }};

        data.uploading = false;
        callback(payload);
        emit('update', data.item, 'not-clear-changes');
    };

    const saveChanges = async (mode) => {
        let scrollTop = trackInfoBox.value?.closest('.item-detail')?.scrollTop;

        data.updating = true;

        const trackInfo = { ...data.updatedItem };

        // Ð£Ð±Ð¸Ñ€Ð°ÐµÐ¼ ÑƒÑ‡Ð°ÑÑ‚Ð½Ð¸ÐºÐ¾Ð², Ð¾Ð½Ð¸ ÑÐµÐ¹Ð²ÑÑ‚ÑÑ Ð¾Ñ‚Ð´ÐµÐ»ÑŒÐ½Ð¾
        delete trackInfo.contributors;

        // Ð£Ð´Ð°Ð»ÑÐµÐ¼ Ð²Ñ€ÐµÐ¼ÐµÐ½Ð½Ñ‹Ð¹ Ð°Ð¹Ð´Ð¸ÑˆÐ½Ð¸Ðº
        delete trackInfo.localId;

        // Ð£Ð±Ð¸Ñ€Ð°ÐµÐ¼ Ð¿Ð¾Ð»Ñ, ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ðµ Ñ€Ð°ÑÑÑ‡Ð¸Ñ‚Ñ‹Ð²Ð°ÑŽÑ‚ÑÑ Ð°Ð²Ñ‚Ð¾Ð¼Ð°Ñ‚Ð¸Ñ‡ÐµÑÐºÐ¸
        delete trackInfo.bpm;
        delete trackInfo.duration;

        // Ð’Ñ‹Ñ‚Ð°ÑÐºÐ¸Ð²Ð°ÐµÐ¼ ÐºÐ°Ñ€Ñ‚Ð¸Ð½ÐºÑƒ
        const cover = trackInfo.cover;
        if (cover) {
            delete trackInfo.cover;
        }

        // Ð’Ñ‹Ñ‚Ð°ÑÐºÐ¸Ð²Ð°ÐµÐ¼ Ñ‚Ñ€ÐµÐº. Ð’Ð¾Ð¾Ð±Ñ‰Ðµ, Ð¾Ð½ Ð½Ð¸ÐºÐ¾Ð³Ð´Ð° Ñ‚ÑƒÑ‚ Ð½Ðµ Ð¾ÐºÐ°Ð¶ÐµÑ‚ÑÑ. ÐÐ¾ Ð¼Ð°Ð»Ð¾ Ð»Ð¸
        const trackFile = trackInfo.trackFile;
        if (trackFile) {
            delete trackInfo.trackFile;
        }

        // Ð£Ð±Ð¸Ñ€Ð°ÐµÐ¼ Ð½Ð° Ð²ÑÑÐºÐ¸Ð¹ ÑÐ»ÑƒÑ‡Ð°Ð¹ Ñ€ÐµÐ¹Ñ‚Ð¸Ð½Ð³, ÐµÑÐ»Ð¸ ÑÑ‚Ð¾ Ð½Ðµ ÐºÑƒÑ€Ð°Ñ‚Ð¾Ñ€ Ñ‚Ñ€ÐµÐºÐ¾Ð²
        if (!canEditRating.value) {
            delete trackInfo.rating;
        }

        // Ð•ÑÐ»Ð¸ Ð¼Ñ‹ Ñ‚Ð¾Ð»ÑŒÐºÐ¾ ÑÐ¾Ð·Ð´Ð°ÐµÐ¼ Ñ‚Ñ€ÐµÐº, Ñ‚Ð¾ Ð½Ð°Ð´Ð¾ Ð¿Ñ€Ð¾ÐºÐ¸Ð½ÑƒÑ‚ÑŒ ÑÑ€Ð°Ð·Ñƒ primary, Ñ‡Ñ‚Ð¾Ð±Ñ‹ Ð¿Ñ€Ð¸Ð²ÑÐ·Ð°Ñ‚ÑŒ
        if (!data.id) {
            const contributor = data.updatedItem.contributors?.find(item => item.role === 'PRIMARY' && item.state === 'CONFIRMED');

            if (contributor) {
                trackInfo.artist = contributor.artist?.id;
                data.updatedItem.contributors = [...data.updatedItem.contributors]
                data.updatedItem.contributors.splice(data.updatedItem.contributors.indexOf(contributor), 1);
            }
        }

        // Ð¡ÐµÐ¹Ð²Ð¸Ð¼ Ð¾ÑÐ½Ð¾Ð²Ð½Ñ‹Ðµ Ð´Ð°Ð½Ð½Ñ‹Ðµ
        let payload;

        // Ð Ð°Ð·Ñ€ÐµÑˆÐµÐ½Ð¸Ðµ Ð½Ð° Ð¸Ð·Ð¼ÐµÐ½ÐµÐ½Ð¸Ðµ
        if (canEdit.value) {
            if (data.id) {
                payload = await tracks.update(data.id, trackInfo);
            } else {
                payload = await tracks.add(trackInfo);

                emit('actions', {
                    item: data.item,
                    actions: []
                });
            }


            // ÐžÐ±Ð½Ð¾Ð²Ð»ÑÐµÐ¼ ÐºÐ°Ñ€Ñ‚Ð¸Ð½ÐºÑƒ
            if (payload?.id && cover) {
                payload = await tracks.updateCover(payload.id, cover);
            }
        } else if (data.id) {
            payload = await tracks.get(data.id);
        }

        if (!payload) {
            return;
        }

        // ÐžÐ±Ð½Ð¾Ð²Ð»ÑÐµÐ¼ ÑƒÑ‡Ð°ÑÑ‚Ð½Ð¸ÐºÐ¾Ð²
        if (canEditContributor.value && data.updatedItem?.contributors) {
            const oldIds = data.item?.contributors?.filter(item => item.id).map(item => item.id) || [];
            const newIds = data.updatedItem?.contributors?.filter(item => item.id).map(item => item.id) || [];

            // Ð£Ð´Ð°Ð»ÑÐµÐ¼ Ð»Ð¸ÑˆÐ½Ð¸Ðµ
            await Promise.all(oldIds.filter(id => newIds.indexOf(id) === -1).map(async (id) => {
                await tracks.removeContributor(id);
            }));

            // ÐžÐ±Ð½Ð¾Ð²Ð»ÑÐµÐ¼ Ð¸Ð·Ð¼ÐµÐ½ÐµÐ½Ð½Ñ‹Ðµ
            await Promise.all(data.updatedItem?.contributors.filter(item => item.id && oldIds.indexOf(item.id) === -1).map(async (item) => {
                await tracks.updateContributor(item.id, {
                    artistId: item.artist?.id,
                    role: item.role,
                    unregisteredArtistName: item.unregisteredArtistName
                });
            }));

            // Ð”Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ð½Ð¾Ð²Ñ‹Ðµ
            await Promise.all(data.updatedItem?.contributors.filter(item => !item.id).map(async (item) => {
                const contributor = await tracks.addContributor({
                    trackId: payload?.id || data.id,
                    artistId: item.artist?.id,
                    role: item.role,
                    unregisteredArtistName: item.unregisteredArtistName
                });

                item.id = contributor?.id;
            }));

            if (!data.id && payload.contributors.length) {
                data.updatedItem?.contributors.unshift(payload.contributors[0]);
            }

            payload.contributors = data.updatedItem?.contributors || [];
        }


        // ÐŸÑ€Ð¸Ð²ÑÐ·Ñ‹Ð²Ð°ÐµÐ¼ Ñ‚Ñ€ÐµÐº Ðº Ñ€ÐµÐ»Ð¸Ð·Ñƒ
        payload = (await addItemToRelease(payload)) || payload;
        // ÐŸÑ€Ð¸Ð²ÑÐ·Ñ‹Ð²Ð°ÐµÐ¼ Ñ‚Ñ€ÐµÐº Ðº Ð¿Ð»ÐµÐ¹Ð»Ð¸ÑÑ‚Ñƒ
        payload = (await addItemToPlaylist(payload)) || payload;

        await delay(200);
        data.updating = false;

        if (payload?.id) {
            message.info(t(`pages.tracks.${ data.id ? 'edit' : 'add' }.done.title`));
            // modals.open('success', {
            //     data: {
            //         timer: 3,
            //         title: t(`pages.tracks.${ data.id ? 'edit' : 'add' }.done.title`),
            //         close: t(`pages.tracks.${ data.id ? 'edit' : 'add' }.done.close`)
            //     },
            //     quietClose: true
            // });
        }

        if (data.id) {
            emit('update', payload, mode);
        } else {
            emit('add', {
                ...payload,
                localId: data.item?.localId
            }, mode);
        }

        data.item = payload;
        data.updatedItem = {};
        data.code = payload.code;
        data.id = payload.id;

        if (props.viewMode === 'list' && scrollTop) {
            setTimeout(() => {
                if (trackInfoBox?.value) {
                    trackInfoBox.value.closest('.item-detail').scrollTop = scrollTop;
                }
            });
        }
    }

    const saveChangesAndAddMore = async () => {
        await saveChanges('add-one');
    }

    const getItem = async () => {
        data.loading = true;

        const payload = await tracks.get(props.track.id);

        // ÐŸÐ¾Ð»ÑƒÑ‡Ð°ÐµÐ¼ Ñ€ÐµÐ»Ð¸Ð·Ñ‹, Ð² ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ñ… ÐµÑÑ‚ÑŒ Ð½Ð°Ñˆ Ñ‚Ñ€ÐµÐº
        await getItemReleases();

        // ÐŸÐ¾Ð»ÑƒÑ‡Ð°ÐµÐ¼ Ð¿Ð»ÐµÐ¹Ð»Ð¸ÑÑ‚Ñ‹, Ð² ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ñ… ÐµÑÑ‚ÑŒ Ð½Ð°Ñˆ Ñ‚Ñ€ÐµÐº
        await getItemPlaylists();

        data.id = payload?.id;
        data.item = payload || {};

        await getStates();

        await delay(200);

        data.loading = false;
    }

    const createItem = () => {
        data.item = {
            instrumental: true
        }

        if (props.track?.localId) {
            data.item = { ...props.track };
            // data.item.localId = props.track.localId;
        }

        if (props.artist && !data.item.contributors) {
            data.item.contributors = [{
                artist: {...props.artist},
                role: 'PRIMARY',
                state: 'CONFIRMED'
            }];
        }

        data.updatedItem = { ...data.item };

        data.states = [];
        data.errors = {};
        data.id = null;

        emit('edit', {
            ...data.item,
            ...data.updatedItem
        });
    }

    // Releases
        const removeItemFromRelease = async (release) => {
            if (!release?.id) {
                return;
            }

            // Ð•ÑÐ»Ð¸ Ñ Ð½Ðµ ÐºÑƒÑ€Ð°Ñ‚Ð¾Ñ€ Ñ‚Ñ€ÐµÐºÐ¾Ð² Ð¸ Ñ€ÐµÐ»Ð¸Ð· Ð¾Ð¿ÑƒÐ±Ð»Ð¸ÐºÐ¾Ð²Ð°Ð½, Ñ‚Ð¾ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÑ‚ÑŒ Ð½ÐµÐ»ÑŒÐ·Ñ
            if (!releases.canAddTrack({
                release: release,
                artist: props.artist,
                track: data.item
            })) {
                return;
            }

            // Ð•ÑÐ»Ð¸ Ñ‚Ð°ÐºÐ¾Ð¹ Ñ€ÐµÐ»Ð¸Ð· Ð¾Ñ‚ÑÑƒÑ‚ÑÑ‚Ð²ÑƒÐµÑ‚ ÑÑ€ÐµÐ´Ð¸ Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð½Ñ‹Ñ…, Ñ‚Ð¾ Ñ‚ÑƒÑ‚ Ð´ÐµÐ»Ð°Ñ‚ÑŒ Ð½ÐµÑ‡ÐµÐ³Ð¾
            if (!data.releases?.find(item => item.id === release.id)) {
                return;
            }


            modals.confirm({
                title: t(`pages.tracks.remove-from-release.confirm.title`),
                body: t(`pages.tracks.remove-from-release.confirm.description`, {
                    'track-title': data.item?.title,
                    'release-title': release?.title
                }),
                onOk: async () => {
                    await releases.tracks.remove({
                        releaseId: release.id,
                        trackId: data.item.id
                    });

                    // ÐÐºÑ‚ÑƒÐ°Ð»Ð¸Ð·Ð¸Ñ€ÑƒÐµÐ¼ ÑÐ¿Ð¸ÑÐ¾Ðº Ñ€ÐµÐ»Ð¸Ð·Ð¾Ð²
                    await getItemReleases();

                    modals.open('success', {
                        data: {
                            title: t(`pages.tracks.remove-from-release.done.title`),
                            close: t(`pages.tracks.remove-from-release.done.close`)
                        },
                        quietClose: true
                    });

                    if (props.release?.id && release.id === props.release.id) {
                        emit('hide', data.item);
                    }
                }
            }); 
        }

        const addItemToRelease = async (payload) => {
            if (!props.release?.id || !payload?.id) {
                return;
            }

            // Ð•ÑÐ»Ð¸ Ñ Ð½Ðµ ÐºÑƒÑ€Ð°Ñ‚Ð¾Ñ€ Ñ‚Ñ€ÐµÐºÐ¾Ð² Ð¸ Ñ€ÐµÐ»Ð¸Ð· Ð¾Ð¿ÑƒÐ±Ð»Ð¸ÐºÐ¾Ð²Ð°Ð½, Ñ‚Ð¾ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÑ‚ÑŒ Ð½ÐµÐ»ÑŒÐ·Ñ
            if (!releases.canAddTrack({
                release: props.release,
                artist: props.artist,
                track: payload
            })) {
                return;
            }

            // Ð•ÑÐ»Ð¸ Ñ‚Ð°ÐºÐ¾Ð¹ Ñ€ÐµÐ»Ð¸Ð· ÑƒÐ¶Ðµ ÐµÑÑ‚ÑŒ, Ñ‚Ð¾ Ñ‚Ð¾Ð¶Ðµ Ð¿Ð¾Ð²Ñ‚Ð¾Ñ€Ð½Ð¾ Ñ‚ÑƒÑ‚ Ð´ÐµÐ»Ð°Ñ‚ÑŒ Ð½ÐµÑ‡ÐµÐ³Ð¾
            if (data.releases?.find(item => item.id === props.release.id)) {
                return;
            }

            const result = await releases.tracks.add({
                releaseId: props.release.id,
                trackId: payload.id
            });

            // ÐÐºÑ‚ÑƒÐ°Ð»Ð¸Ð·Ð¸Ñ€ÑƒÐµÐ¼ ÑÐ¿Ð¸ÑÐ¾Ðº Ñ€ÐµÐ»Ð¸Ð·Ð¾Ð²
            await getItemReleases();

            // Ð£ÑÑ‚Ð°Ð½Ð°Ð²Ð»Ð¸Ð²Ð°ÐµÐ¼ Ð¿Ð¾Ð»Ð¾Ð¶ÐµÐ½Ð¸Ðµ Ñ‚Ñ€ÐµÐºÐ° Ð² Ñ€ÐµÐ»Ð¸Ð·Ðµ
            if (props.position) {
                await updateItemPosition(props.position);
            }

            return result;
        }

        const getItemReleases = async () => {
            const payload = await releases.list({
                tracks: [props.track.id]
            });

            if (payload?.items) {
                data.releases = payload.items || [];
            }
        }

    // Playlists
        const removeItemFromPlaylist = async (playlist) => {
            if (!playlist?.id) {
                return;
            }

            // Ð•ÑÐ»Ð¸ Ñ Ð½Ðµ ÐºÑƒÑ€Ð°Ñ‚Ð¾Ñ€ Ñ‚Ñ€ÐµÐºÐ¾Ð², Ñ‚Ð¾ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÑ‚ÑŒ Ð½ÐµÐ»ÑŒÐ·Ñ
            if (!playlists.canAddTrack({
                playlist: playlist,
                artist: props.artist,
                track: data.item
            })) {
                return;
            }

            // Ð•ÑÐ»Ð¸ Ñ‚Ð°ÐºÐ¾Ð¹ Ñ€ÐµÐ»Ð¸Ð· Ð¾Ñ‚ÑÑƒÑ‚ÑÑ‚Ð²ÑƒÐµÑ‚ ÑÑ€ÐµÐ´Ð¸ Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð½Ñ‹Ñ…, Ñ‚Ð¾ Ñ‚ÑƒÑ‚ Ð´ÐµÐ»Ð°Ñ‚ÑŒ Ð½ÐµÑ‡ÐµÐ³Ð¾
            if (!data.playlists?.find(item => item.id === playlist.id)) {
                return;
            }


            modals.confirm({
                title: t(`pages.tracks.remove-from-playlist.confirm.title`),
                body: t(`pages.tracks.remove-from-playlist.confirm.description`, {
                    'track-title': data.item?.title,
                    'playlist-title': playlist?.title ? playlist.title['en-US'] || playlist.title : playlist?.title
                }),
                onOk: async () => {
                    await playlists.tracks.remove({
                        playlistId: playlist.id,
                        trackId: data.item.id
                    });

                    // ÐÐºÑ‚ÑƒÐ°Ð»Ð¸Ð·Ð¸Ñ€ÑƒÐµÐ¼ ÑÐ¿Ð¸ÑÐ¾Ðº Ð¿Ð»ÐµÐ¹Ð»Ð¸ÑÑ‚Ð¾Ð²
                    await getItemPlaylists();

                    modals.open('success', {
                        data: {
                            title: t(`pages.tracks.remove-from-playlist.done.title`),
                            close: t(`pages.tracks.remove-from-playlist.done.close`)
                        },
                        quietClose: true
                    });

                    if (props.playlist?.id && playlist.id === props.playlist.id) {
                        emit('hide', data.item);
                    }
                }
            }); 
        }

        const addItemToPlaylist = async (payload) => {
            if (!props.playlist?.id || !payload?.id) {
                return;
            }

            // Ð•ÑÐ»Ð¸ Ñ Ð½Ðµ ÐºÑƒÑ€Ð°Ñ‚Ð¾Ñ€ Ð¿Ð»ÐµÐ¹Ð»Ð¸ÑÑ‚, Ñ‚Ð¾ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÑ‚ÑŒ Ð½ÐµÐ»ÑŒÐ·Ñ
            if (!playlists.canAddTrack({
                playlist: props.playlist,
                artist: props.artist,
                track: payload
            })) {
                return;
            }
            

            // Ð•ÑÐ»Ð¸ Ñ‚Ð°ÐºÐ¾Ð¹ Ð¿Ð»ÐµÐ¹Ð»Ð¸ÑÑ‚ ÑƒÐ¶Ðµ ÐµÑÑ‚ÑŒ, Ñ‚Ð¾ Ñ‚Ð¾Ð¶Ðµ Ð¿Ð¾Ð²Ñ‚Ð¾Ñ€Ð½Ð¾ Ñ‚ÑƒÑ‚ Ð´ÐµÐ»Ð°Ñ‚ÑŒ Ð½ÐµÑ‡ÐµÐ³Ð¾
            if (data.playlists?.find(item => item.id === props.playlist.id)) {
                return;
            }

            const result = await playlists.tracks.add({
                playlistId: props.playlist.id,
                trackId: payload.id
            });

            // ÐÐºÑ‚ÑƒÐ°Ð»Ð¸Ð·Ð¸Ñ€ÑƒÐµÐ¼ ÑÐ¿Ð¸ÑÐ¾Ðº Ð¿Ð»ÐµÐ¹Ð»Ð¸ÑÑ‚Ð¾Ð²
            await getItemPlaylists();

            // Ð£ÑÑ‚Ð°Ð½Ð°Ð²Ð»Ð¸Ð²Ð°ÐµÐ¼ Ð¿Ð¾Ð»Ð¾Ð¶ÐµÐ½Ð¸Ðµ Ñ‚Ñ€ÐµÐºÐ° Ð² Ð¿Ð»ÐµÐ¹Ð»Ð¸ÑÑ‚Ðµ
            if (props.position) {
                await updateItemPosition(props.position);
            }

            return result;
        }

        const getItemPlaylists = async () => {
            // const payload = await playlists.list({
            //     tracks: [props.track.id]
            // });

            // if (payload?.items) {
            //     data.playlists = payload.items || [];
            // }
        }

    const updateItemPosition = async (position) => {
        if (!data.item?.id) {
            return;
        }

        if (props.release?.id) {
            await releases.tracks.setPosition({
                releaseId: props.release?.id,
                trackId: data.item.id,
                position
            });
        }

        if (props.playlist?.id) {
            await playlists.tracks.setPosition({
                playlistId: props.playlist?.id,
                trackId: data.item.id,
                position
            });
        }
    }

    watch(
        () => saveDisabled.value,
        (value) => {
            if (value) {
                emit('actions', {
                    item: data.item,
                    actions: []
                });
            } else {
                emit('actions', {
                    item: data.item,
                    actions: [
                        {
                            type: props.track?.id ? 'save' : 'create',
                            action: saveChanges
                        }
                    ]
                });
            }
        }
    );

    // watch(
    //     () => props.position,
    //     (position) => {
    //         updateItemPosition(position);
    //     }
    // );

    onMounted(async () => {
        if (props.track?.id) {
            await getItem();

            if (props.release?.id) {
                // Ð’Ñ‚Ð¸Ñ…ÑƒÑŽ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ñ‚Ñ€ÐµÐº Ðº Ñ€ÐµÐ»Ð¸Ð·Ñƒ
                addItemToRelease(data.item);
            }
            if (props.playlist?.id) {
                // Ð’Ñ‚Ð¸Ñ…ÑƒÑŽ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ñ‚Ñ€ÐµÐº Ðº Ð¿Ð»ÐµÐ¹Ð»Ð¸ÑÑ‚Ñƒ
                addItemToPlaylist(data.item);
            }
        } else {
            createItem();
            data.loading = false;
        }
    });
</script>

