        &.view-mode-table {
            flex-direction: row;
            gap: 0;

            .actions-box {
                width: 140px;
                box-shadow: -8px 0px 8px -12px var(--dark-500);
                z-index: 1;
                position: sticky;
                right: 0;
                flex-direction: column-reverse;
                justify-content: start;
                padding: 18px 8px;
            }

            &.updating {
                pointer-events: none;
                position: relative;
                &:after {
                    content: '';
                    opacity: .6;
                    @include skeleton;
                }

                .actions-box {
                    pointer-events: none;
                }
            }
        }
    }
    .direction-rtl .track-info {
        &.view-mode-table {
            .actions-box {
                right: auto;
                left: 0;
                box-shadow: 8px 0px 8px -12px var(--dark-500);
            }
