<template>
    <!-- From Project -->
    <FormLine :field="fields.fromProject" :viewMode="props.viewMode">
        <Field
            :placeholder="t(`pages.tracks.fields.from-project.name`)"
            :value="data.fromProject"
            :disabled="!props.permissions.canEdit"
            class="noerror light with-placeholder"
            @change="onFromProjectChange"
        />
    </FormLine>

    <!-- Version -->
    <FormLine :field="fields.version" :viewMode="props.viewMode">
        <Select
            class="noerror light"
            :optionList="TRACK_VERSIONS"
            :placeholder="t(`pages.tracks.fields.version.name`)"
            :disabled="!props.permissions.canEdit"
            :value="data.version"
            :leftAuto="false"
            :maxValuesCount="1"
            :rightAuto="false"
            :useI18n="true"
            @change="onTrackVersionChange"
        />
    </FormLine>
</template>

<script setup>
    import { reactive } from 'vue';
    import { useI18n } from 'vue-i18n';
    
    import Field from 'ðŸ”—/components/ui/Field.vue';
    import Select from 'ðŸ”—/components/ui/Select.vue';
    import FormLine from '@/components/ui/FormLine.vue';
    import { TRACK_VERSIONS } from 'ðŸ”—/constants';

    const emit = defineEmits(['change', 'error', 'metadata', 'save']);
    const { t } = useI18n();

    const props = defineProps({
        permissions: { type: Object, default: () => ({}) },
        item: { type: [Object, null], default: null },
        viewMode: { type: String, default: 'list' }
    });

    const fields = {
        fromProject: {
            name: t(`pages.tracks.fields.from-project.name`),
            attention: t(`pages.tracks.fields.from-project.attention`),
            width: 200
        },
        version: {
            name: t(`pages.tracks.fields.version.name`),
            attention: t(`pages.tracks.fields.version.attention`),
            width: 200
        }
    }

    emit('metadata', {
        fields,
        direction: 'row'
    });

    const data = reactive({
        fromProject: props.item?.fromProject || '',
        version: props.item?.version
    });

    const onFromProjectChange = (e) => {
        data.fromProject = e.target.value?.trim();

        emit('change', { fromProject: data.fromProject });
    }

    const onTrackVersionChange = (e) => {
        if (!e) {
            return;
        }

        data.version = e.value;

        emit('change', { version: data.version });
    }
</script>

<style lang="scss" scoped>
</style>
