<template>
    <!-- Title -->
    <FormLine :field="fields.title" :viewMode="props.viewMode">
        <div class="title-box">
            <Field
                :placeholder="t(`pages.tracks.fields.title.placeholder`)"
                :value="data.title"
                :autofocus="!data.title"
                :disabled="!props.permissions.canEdit"
                class="noerror light with-placeholder"
                @change="onTitleChange"
                @pressEnter="onTitleEnter"
            />

            <template v-if="!props.item.id && props.viewMode === 'list'">
                <Button 
                    :name="t('pages.tracks.fields.title.continue')"
                    class="size-s reverse"
                    :disabled="disabled || !props.permissions.canEdit"
                    icon="chevron-right"
                    @click="emit('save')"
                />
                <Icon icon="help" v-tooltip="t('pages.tracks.sections.track-info.help')" class="help"/>
            </template>
        </div>
    </FormLine>
    <FormLine :field="fields.trackFile" :viewMode="props.viewMode" v-if="!props.item.id && props.viewMode === 'table'">
        <Button 
            :name="t('pages.tracks.fields.title.continue')"
            class="size-m reverse"
            :disabled="disabled || !props.permissions.canEdit"
            icon="chevron-right"
            @click="emit('save')"
        />
    </FormLine>

    <!-- Track file -->
    <FormLine :field="fields.trackFile"
        :viewMode="props.viewMode"
        v-show="props.item.id"
        :class="{ 'align-center' : props.viewMode === 'list' }"
    >
        <div class="track-box" :class="{ table: props.viewMode === 'table' }">
            <TrackWave v-if="!data.loading && props.item?.trackFile"
                :wave="props.item.wave"
                :id="props.item.id"
                :duration="props.item.duration"
                :simplify="props.viewMode === 'table' ? 5 : 1"
            />
            <FileUploader 
                class="upload"
                :acceptedFormats="['.wav', 'audio/wav', 'audio/x-wav']"
                :dropZone="!props.item?.trackFile || data.loading"
                :disabled="!props.permissions.canEdit"
                :loading="data.loading"
                description="pages.tracks.fields.track-file.name"
                @change="onTrackFileChange"
            />
        </div>
    </FormLine>
</template>

<script setup>
    import { reactive, computed } from 'vue';
    import { useI18n } from 'vue-i18n';
    
    import Field from 'ðŸ”—/components/ui/Field.vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import FormLine from '@/components/ui/FormLine.vue';
    import FileUploader from 'ðŸ”—/components/ui/FileUploader.vue';
    import TrackWave from 'ðŸ”—/components/ui/TrackWave.vue';

    const emit = defineEmits(['change', 'error', 'metadata', 'save']);
    const { t } = useI18n();

    const props = defineProps({
        permissions: { type: Object, default: () => ({}) },
        item: { type: [Object, null], default: null },
        viewMode: { type: String, default: 'list' }
    });

    const fields = {
        title: {
            name: t(`pages.tracks.fields.title.name`),
            attention: t(`pages.tracks.fields.title.attention`),
            attentions: [
                t(`pages.tracks.fields.title.subattention1`),
                t(`pages.tracks.fields.title.subattention2`),
                t(`pages.tracks.fields.title.subattention3`)
            ],
            width: 200
        },
        trackFile: {
            name: t(`pages.tracks.fields.track-file.name`),
            attention: t(`pages.tracks.fields.track-file.attention`),
            width: 200
        },
    };

    emit('metadata', {
        fields,
        direction: 'row'
    });

    const data = reactive({
        title: props.item?.title || '',
        loading: false
    });

    const disabled = computed(() => {
        return !(data.title || '').trim().length;
    })

    const onTitleChange = (e) => {
        data.title = e.target.value?.trim();

        emit('change', { title: data.title });
    }

    const onTitleEnter = (e) => {
        if (props.item?.id || disabled.value) {
            return;
        }

        emit('save', e.ctrlKey ? 'add-one' : null);
    }

    const onTrackFileChange = (e) => {
        data.loading = true;

        emit('change', {
            trackFile: {
                file: e?.name ? e : null,
                callback: (payload) => {
                    data.loading = false;
                }
            }
        });
    }
</script>

<style lang="scss" scoped>
    .title-box {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 24px;

        .field {
            flex: 1 1;
            max-width: var(--input-max-width);
        }
    }

    .track-box {
        display: flex;
        flex-direction: row;
        align-items: center;
        gap: 24px;
        .dropzone-upload {
            flex: 1;
            // min-height: 100px;
        }
        .track-wave {
            flex: 1;
            width: 1%;
            height: 100px;
        }

        &.table {
            gap: 8px;
            .track-wave {
                height: 38px;
            }
        }
    }

    .help {
        @include studio.help-icon;
    }
</style>
