<template>
    <!-- Isrc -->
    <FormLine :field="fields.isrc" :viewMode="props.viewMode">
        <Field
            :placeholder="t(`pages.tracks.fields.isrc.placeholder`)"
            :value="data.isrc"
            :mask="maskISRC"
            :disabled="!props.permissions.canEdit"
            :isError="!!data.errors.isrc?.length"
            class="noerror light with-placeholder"
            @change="onISRCChange"
        />
    </FormLine>


    <!-- Iswc -->
    <FormLine :field="fields.iswc" :viewMode="props.viewMode">
        <Field
            :placeholder="t(`pages.tracks.fields.iswc.placeholder`)"
            :value="data.iswc"
            :mask="maskISWC"
            :disabled="!props.permissions.canEdit"
            :isError="!!data.errors.iswc?.length"
            class="noerror light with-placeholder"
            @change="onISWCChange"
        />
    </FormLine>

    <!-- Bpm -->
    <!-- <FormLine :field="fields.bpm" :viewMode="props.viewMode">
        <Field
            :placeholder="t(`pages.tracks.fields.bpm.placeholder`)"
            :value="data.bpm"
            type="number"
            :min="40"
            :disabled="!props.permissions.canEdit"
            :max="500"
            readonly
            class="noerror light with-placeholder"
            @change="onBPMChange"
        />
    </FormLine> -->

    <!-- Cover -->
    <FormLine :field="fields.cover" :viewMode="props.viewMode">
        <div class="image-preview">
            <FileUploader 
                class="upload"
                icon="upload-img"
                :acceptedFormats="['image/png', 'image/jpeg', 'image/jpg', 'image/gif', 'image/jfif']"
                :disabled="!props.permissions.canEdit"
                :dropZone="true"
                :showPreview="true"
                :zoneSize="{
                    width: props.viewMode === 'table' ? '100px' : '180px',
                    height: props.viewMode === 'table' ? '100px' : '180px'
                }"
                :preview="data.cover?.resized"
                zoneType="square-image"
                @change="onCoverChange"
            />
        </div>
    </FormLine>

    <!-- Release date -->
    <FormLine :field="fields.releaseDate" :viewMode="props.viewMode">
        <DateField
            :text-input="true"
            :enableTimePicker="false"
            :timePicker="false"
            :disabled="!props.permissions.canEdit"
            :placeholder="t(`pages.tracks.fields.release-date.placeholder`)"
            format="dd.MM.yyyy"
            class="light noerror with-placeholder"
            :modelValue="data.releaseDate"
            @update:modelValue="onReleaseDateChange"
        />
    </FormLine>
</template>

<script setup>
    import { reactive } from 'vue';
    import { useI18n } from 'vue-i18n';
    
    import Field from 'ðŸ”—/components/ui/Field.vue';
    import FormLine from '@/components/ui/FormLine.vue';
    import FileUploader from 'ðŸ”—/components/ui/FileUploader.vue';
    import DateField from 'ðŸ”—/components/ui/DateField.vue';
    import { maskISRC, maskISWC } from 'ðŸ”—/utils/utils';
    import moment from 'moment';

    const emit = defineEmits(['change', 'error', 'metadata', 'save']);
    const { t } = useI18n();

    const props = defineProps({
        permissions: { type: Object, default: () => ({}) },
        item: { type: [Object, null], default: null },
        viewMode: { type: String, default: 'list' }
    });

    const fields = {
        isrc: {
            name: t(`pages.tracks.fields.isrc.name`),
            attentions: [
                t(`pages.tracks.fields.isrc.subattention1`)
            ],
            width: 220
        },
        iswc: {
            name: t(`pages.tracks.fields.iswc.name`),
            attentions: [
                t(`pages.tracks.fields.iswc.subattention1`)
            ],
            width: 220
        },
        // bpm: {
        //     name: t(`pages.tracks.fields.bpm.name`),
        //     attention: t(`pages.tracks.fields.bpm.attention`),
        //     width: 200
        // },
        cover: {
            name: t(`pages.tracks.fields.cover.name`),
            attention: t(`pages.tracks.fields.cover.attention`),
            attentions: [
                t(`pages.tracks.fields.cover.subattention1`),
                t(`pages.tracks.fields.cover.subattention2`),
                t(`pages.tracks.fields.cover.subattention3`)
            ],
            width: 116
        },
        releaseDate: {
            name: t(`pages.tracks.fields.release-date.name`),
            width: 210
        },
    };

    emit('metadata', {
        fields,
        direction: 'row'
    });

    const data = reactive({
        isrc: props.item?.isrc,
        iswc: props.item?.iswc,
        bpm: props.item?.bpm,
        releaseDate: props.item?.releaseDate,
        cover: props.item?.cover || {},

        errors: {}
    });


    const onISRCChange = (e) => {
        data.isrc = e.target.typedValue?.trim().toUpperCase();

        if (props.item?.isrc !== data.isrc) {
            emit('change', { isrc: data.isrc });
        }

        data.errors.isrc = [];
        if (data.isrc && !e.target.masked.isComplete) {
            data.errors.isrc = [t(`pages.tracks.fields.isrc.error`)];
        }

        emit('error', data.errors);
    }

    const onISWCChange = (e) => {
        data.iswc = e.target.typedValue?.trim().toUpperCase();

        if (props.item?.iswc !== data.iswc) {
            emit('change', { iswc: data.iswc });
        }

        data.errors.iswc = [];
        if (data.iswc && !e.target.masked.isComplete) {
            data.errors.iswc = [t(`pages.tracks.fields.iswc.error`)];
        }

        emit('error', data.errors);
    }

    const onBPMChange = (e) => {
        data.bpm = +e.target.value || null;

        emit('change', { bpm: data.bpm });
    }

    const onCoverChange = (e) => {
        emit('change', { cover: e?.name ? e : null });
    }

    const onReleaseDateChange = (value) => {
        if (!value) {
            data.releaseDate = null;
        } else {
            data.releaseDate = moment(value).toISOString();
        }

        emit('change', { releaseDate: data.releaseDate });
    }
</script>

<style lang="scss" scoped>
</style>
