<template>
    <div class="form-cols"
        :class="{ 'view-mode-table': props.viewMode === 'table' }"
    >
        <div>
            <FormLine :field="fields.soundQuality" :viewMode="props.viewMode">
                <Range
                    :value="data.rating.sound_quality"
                    :disabled="!props.permissions.canEditRating"
                    @change="onSoundQualityChange"
                />
            </FormLine>

            <FormLine :field="fields.melody" :viewMode="props.viewMode">
                <Range
                    :value="data.rating.melody"
                    :disabled="!props.permissions.canEditRating"
                    @change="onMelodyChange"
                />
            </FormLine>

            <FormLine :field="fields.curatorScore" :viewMode="props.viewMode">
                <Range
                    :value="data.rating.curator_score"
                    :disabled="!props.permissions.canEditRating"
                    @change="onCuratorScoreChange"
                />
            </FormLine>
        </div>
        <div class="col-total">
            <Progress
                v-if="props.viewMode === 'list'"
                :value="100 * ratingTotal / 300"
                :title="t(`pages.tracks.fields.rating.total.name`, { total: ratingTotal})"
            />
            <span v-if="props.viewMode === 'table'">
                {{ t(`pages.tracks.fields.rating.total.name`, { total: ratingTotal}) }}
            </span>
        </div>
    </div>
</template>

<script setup>
    import { reactive, computed } from 'vue';
    import { useI18n } from 'vue-i18n';
    
    import Range from 'ðŸ”—/components/ui/Range.vue';
    import Progress from 'ðŸ”—/components/ui/Progress.vue';
    import FormLine from '@/components/ui/FormLine.vue';

    const emit = defineEmits(['change', 'error', 'metadata', 'save']);
    const { t } = useI18n();

    const props = defineProps({
        permissions: { type: Object, default: () => ({}) },
        item: { type: [Object, null], default: null },
        viewMode: { type: String, default: 'list' }
    });

    const fields = {
        soundQuality: {
            name: t(`pages.tracks.fields.rating.sound-quality.name`),
            attentions: [
                t(`pages.tracks.fields.rating.sound-quality.attention`)
            ],
            width: 140
        },
        melody: {
            name: t(`pages.tracks.fields.rating.melody.name`),
            attentions: [
                t(`pages.tracks.fields.rating.melody.attention`)
            ],
            width: 140
        },
        curatorScore: {
            name: t(`pages.tracks.fields.rating.curator-score.name`),
            attentions: [
                t(`pages.tracks.fields.rating.curator-score.attention`)
            ],
            width: 140
        },
        total: {
            width: 140
        }
    };

    emit('metadata', {
        fields,
        direction: 'row'
    });

    const data = reactive({
        rating: props.item?.rating || {
            sound_quality: 0,
            melody: 0,
            curator_score: 0
        }
    });

    const ratingTotal = computed(() => {
        return (data.rating?.curator_score || 0) +
            (data.rating?.melody || 0) +
            (data.rating?.sound_quality || 0);
    });

    const onCuratorScoreChange = (payload) => {
        data.rating.curator_score = payload;

        emit('change', { rating: data.rating });
    }

    const onMelodyChange = (payload) => {
        data.rating.melody = payload;

        emit('change', { rating: data.rating });
    }

    const onSoundQualityChange = (payload) => {
        data.rating.sound_quality = payload;

        emit('change', { rating: data.rating });
    }
</script>

<style lang="scss" scoped>
    .form-cols {
        display: flex;
        flex-direction: row;
        align-items: start;
        gap: 24px;

        & > div {
            display: flex;
            flex-direction: column;
            gap: 24px;
            flex: 1 1;
        }

        &.view-mode-table {
            gap: 0;
            & > div {
                flex-direction: row;
                gap: 0;
                margin: 0;
                min-width: 0;
                max-width: none;
                flex: none;

                .form-line {
                    margin-top: 20px;
                    padding: 8px 16px;
                }
            }
            .col-total {
                padding: 24px 8px;
                span {
                    font: var(--font-body-1);
                    font-weight: bold;
                    color: var(--dark-500);
                }
            }
        }
    }
    
    .col-total {
        display: flex;
        flex-shrink: 0;
        max-width: 230px;
        min-width: 230px;
        align-items: center;
        justify-content: center;
    }
</style>
