<template>
    <ItemsEditor
        :t="data.t"
        :code="data.code"
        :canAdd="canAdd"
        :getItem="api.tracks().get"
        :selectItems="selectItems"
        :sortable="sortable && (!!props.release?.id || !!props.playlist?.id)"
        :items="props.items"
        @update="emit('update', $event)"
        @add="emit('add', $event)"
        @remove="emit('remove', $event)"
        @close="emit('close', $event)"
        @sort="onSort"
        ref="editor"
    >
        <template #page-header-title>
            <template v-if="props.artist || props.release || props.playlist">
                <PageHeaderArtistTitle :artist="props.artist" v-if="props.artist" />

                <Icon icon="chevron-right" class="devider" v-if="props.artist && (props.release || props.playlist)" />

                <PageHeaderReleaseTitle :release="props.release" v-if="props.release" />
                <PageHeaderPlaylistTitle :playlist="props.playlist" v-if="props.playlist" />
            </template>

            <label v-else class="title">{{ t(`pages.tracks.title`) }}</label>
        </template>


        <template #items="{ id, items, change, sortable, sort }">
            <Tracks
                :id="id"
                :items="items"
                :sortable="sortable"
                @change="change"
                @sort="sort"
            />
        </template>

        <template #preview="{ item }">
            <TrackPreview :item="item"
                :showState="true"
            />
        </template>
        
        <template #item="{ toolbarId, position, item, viewMode, edit, add, update, remove, close, sections, hide, actions }">
            <TrackInfo
                :toolbarId="toolbarId"
                :position="position"
                :track="item"
                :viewMode="viewMode"
                :artist="props.artist"
                :release="props.release"
                :playlist="props.playlist"
                @edit="edit"
                @add="add"
                @update="update"
                @remove="remove"
                @close="close"
                @hide="hide"
                @sections="sections"
                @actions="actions"
            />
        </template>
    </ItemsEditor>
</template>

<script setup>
    import { useI18n } from 'vue-i18n';
    import { reactive, computed, onMounted, ref } from 'vue';

    import ItemsEditor from '@/components/ui/ItemsEditor/ItemsEditor.vue';
    import PageHeaderArtistTitle from '@/components/blocks/PageHeader/PageHeaderArtistTitle.vue';
    import PageHeaderReleaseTitle from '@/components/blocks/PageHeader/PageHeaderReleaseTitle.vue';
    import PageHeaderPlaylistTitle from '@/components/blocks/PageHeader/PageHeaderPlaylistTitle.vue';

    import TrackInfo from '../TrackInfo/TrackInfo.vue';
    import Tracks from './Tracks.vue';
    import TrackPreview from 'ðŸ”—/components/ui/TrackPreview.vue';
    import api from 'ðŸ”—/api';
    import stores from '@/stores';

    const { t } = useI18n();
    const emit = defineEmits(['update', 'add', 'remove', 'close']);

    const releases = api.releases();
    const playlists = api.playlists();
    const modals = stores.modals();

    const editor = ref(null);


    const props = defineProps({
        artist: { type: [Object, null], default: null },
        release: { type: [Object, null], default: null },
        playlist: { type: [Object, null], default: null },
        items: { type: [Array, null], default: null }
    });

    const data = reactive({
        t: {
            title: 'pages.tracks.title',
            back: 'pages.tracks.back',
            'add-one': 'pages.tracks.add-one',
            'select-several': 'pages.tracks.select-several',
            empty: {
                title: 'pages.tracks.empty.title',
                description: 'pages.tracks.empty.description'
            },
            revert: {
                confirm: {
                    title: 'pages.tracks.revert.confirm.title',
                    description: 'pages.tracks.revert.confirm.description'
                }
            },
            unsaved: {
                confirm: {
                    title: 'pages.tracks.unsaved.confirm.title',
                    description: 'pages.tracks.unsaved.confirm.description'
                }
            }
        },
        code: 'track-editor',
    });

    const canAdd = computed(() => {
        return api.tracks().canAdd({
            artist: props.artist,
            release: props.release,
            playlist: props.playlist
        });
    });

    const sortable = computed(() => {
        if (props.release) {
            return api.releases().canEdit({
                artist: props.artist,
                release: props.release,
                playlist: props.playlist
            });
        }

        if (props.playlist) {
            return api.playlists().canEdit({
                artist: props.artist,
                release: props.release,
                playlist: props.playlist
            });
        }

        return false;
    });

    const create = (count) => {
        editor.value.create(count);
    }

    const selectItems = (items) => {
        const offset = items.length;

        modals.open('select-tracks', {
            data: {
                items,
                artist: props.artist
            },
            callback: (items) => {
                items?.forEach((item, id) => {
                    // Ð”Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ðº Ñ€ÐµÐ»Ð¸Ð·Ñƒ
                    addItemToRelease(item, offset + 1 + id);

                    // Ð”Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ðº Ð¿Ð»ÐµÐ¹Ð»Ð¸ÑÑ‚Ñƒ
                    addItemToPlaylist(item, offset + 1 + id);

                    // Ð”Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼ Ðº Ñ€ÐµÐ´Ð°ÐºÑ‚Ð¾Ñ€
                    editor.value.add(item);
                });
            }
        });
    }

    const addItemToRelease = async (payload, position) => {
        if (!props.release?.id || !payload?.id) {
            return;
        }

        // Ð•ÑÐ»Ð¸ Ñ Ð½Ðµ ÐºÑƒÑ€Ð°Ñ‚Ð¾Ñ€ Ñ‚Ñ€ÐµÐºÐ¾Ð² Ð¸ Ñ€ÐµÐ»Ð¸Ð· Ð¾Ð¿ÑƒÐ±Ð»Ð¸ÐºÐ¾Ð²Ð°Ð½, Ñ‚Ð¾ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÑ‚ÑŒ Ð½ÐµÐ»ÑŒÐ·Ñ
        if (!api.releases().canAddTrack({
            artist: props.artist,
            release: props.release,
            track: payload,
            playlist: props.playlist
        })) {
            return;
        }

        await releases.tracks.add({
            releaseId: props.release.id,
            trackId: payload.id
        });


        // Ð£ÑÑ‚Ð°Ð½Ð°Ð²Ð»Ð¸Ð²Ð°ÐµÐ¼ Ð¿Ð¾Ð»Ð¾Ð¶ÐµÐ½Ð¸Ðµ Ñ‚Ñ€ÐµÐºÐ° Ð² Ñ€ÐµÐ»Ð¸Ð·Ðµ
        await updateItemPosition(payload.id, position);
    }

    const addItemToPlaylist = async (payload, position) => {
        if (!props.playlist?.id || !payload?.id) {
            return;
        }

        // Ð•ÑÐ»Ð¸ Ñ Ð½Ðµ ÐºÑƒÑ€Ð°Ñ‚Ð¾Ñ€ Ñ‚Ñ€ÐµÐºÐ¾Ð² Ð¸ Ñ€ÐµÐ»Ð¸Ð· Ð¾Ð¿ÑƒÐ±Ð»Ð¸ÐºÐ¾Ð²Ð°Ð½, Ñ‚Ð¾ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÑ‚ÑŒ Ð½ÐµÐ»ÑŒÐ·Ñ
        if (!api.playlists().canAddTrack({
            artist: props.artist,
            release: props.release,
            track: payload,
            playlist: props.playlist
        })) {
            return;
        }

        await playlists.tracks.add({
            playlistId: props.playlist.id,
            trackId: payload.id
        });


        // Ð£ÑÑ‚Ð°Ð½Ð°Ð²Ð»Ð¸Ð²Ð°ÐµÐ¼ Ð¿Ð¾Ð»Ð¾Ð¶ÐµÐ½Ð¸Ðµ Ñ‚Ñ€ÐµÐºÐ° Ð² Ñ€ÐµÐ»Ð¸Ð·Ðµ
        await updateItemPosition(payload.id, position);
    }

    const updateItemPosition = async (id, position) => {
        if (!id || !position) {
            return;
        }

        if (props.release?.id) {
            await releases.tracks.setPosition({
                releaseId: props.release?.id,
                trackId: id,
                position
            });
        }

        if (props.playlist?.id) {
            await playlists.tracks.setPosition({
                playlistId: props.playlist?.id,
                trackId: id,
                position
            });
        }
    }

    const onSort = (payload) => {
        if (!payload?.items?.length) {
            return;
        }

        payload.items.forEach((item, id) => {
            if (item.position !== id + 1) {
                if (id === payload.newIndex) {
                    updateItemPosition(item.id, id + 1);
                }

                item.position = id + 1;
            }
        });
    }

    onMounted(() => {
        if (props.items?.length) {
            data.item = props.items[0];
        }
    });

    defineExpose({
        create
    });
</script>

<style lang="scss" scoped>
    .title {
        font: var(--font-h6);
        font-weight: bold;
        color: var(--dark-500);
    }
</style>
