<template>
    <AutoComplete
        v-bind="props"

        :optionList="data.optionList || []"
        @search="onAutoCompleteSearch"
        @change="emit('change', $event)"
    />
</template>

<script setup>
    import { reactive, onMounted } from 'vue';
    import AutoComplete from 'ðŸ”—/components/ui/AutoComplete.vue';
    import api from 'ðŸ”—/api';
    import { code } from 'ðŸ”—/utils/utils';

    const emit = defineEmits(['change']);

    const props = defineProps({
        dict: { type: String, default: '' }
    });

    const data = reactive({
        code: code(),
        optionList: []
    });

    const onAutoCompleteSearch = async (value) => {
        if (!props.dict || !api.dict()[props.dict]) {
            return;
        }
        const searchStr = value?.toLowerCase().trim();

        const params = {
            skip: 0,
            take: 20,
            sort: 'title',
            sort_dir: 'asc'
        }

        if (searchStr?.trim()) {
            params.title = searchStr.trim().toLowerCase();
        }

        const payload = await api.dict()[props.dict].list(params, data.code);

        if (!payload?.items) {
            data.optionList = [];
            return;
        }

        data.optionList = await payload.items.map(item => ({ code: item.code, name: item.title, id: item.id }));
    }

    onMounted(() => {
        onAutoCompleteSearch();
    })
</script>
