<template>
    <div class="items-list">
        <template v-if="props.items.length">
            <draggable
                v-model="props.items"
                :item-key="(item) => item.id || item.localId"
                tag="ul"
                handle=".sort-button"
                ghost-class="dragging"
                @end="onSortEnd"
                class="items"
            >
                <template #item="{ element: item }">
                    <li :class="{ active: props.id === item.id || props.id === item.localId }"
                        @click="emit('change', item)"
                    >
                        <Button icon="drag"
                            v-if="props.sortable"
                            class="tertiary submenu size-m sort-button" />
                        <TrackPreview :item="item" :showState="props.showState" />
                    </li>
                </template>
            </draggable>
        </template>

        <EmptyLabel
            v-if="!props.items.length"
            class="empty"
            :title="t(`pages.tracks.empty.title`)"
            :description="t(`pages.tracks.empty.description`)"
        />
    </div>
</template>

<script setup>
    import { useI18n } from 'vue-i18n';
    import { onMounted, computed } from 'vue';
    import draggable from 'vuedraggable'
    import api from 'ðŸ”—/api';
    import EmptyLabel from 'ðŸ”—/components/ui/EmptyLabel.vue';
    import TrackPreview from 'ðŸ”—/components/ui/TrackPreview.vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';

    const emit = defineEmits(['change', 'sort']);

    const { t } = useI18n();

    const props = defineProps({
        items: { type: Array, required: true },
        sortable: { type: Boolean, default: false },
        showState: { type: Boolean, default: true },
        id: { type: [String, null], default: null }
    });

    const onSortEnd = (evt) => {
        const movedItem = props.items.splice(evt.oldIndex, 1)[0]
        props.items.splice(evt.newIndex, 0, movedItem);

        emit('sort', { 
            oldIndex: evt.oldIndex,
            newIndex: evt.newIndex,
        });
    }
</script>

<style lang="scss" scoped>
    @include studio.default-items-list;

    .items-list {
        .items {
            li {
                padding: 8px;

                .sort-button {
                    margin-left: -6px;
                    cursor: grab;
                }
            }
        }
    }
</style>