<template>
    <IconButton
        v-if="isFullScreen"
        icon="minimize"
        :title="t('ui.full-screen-toggle.exit')"
        :class="{ invert: props.invert }"
        class="size-m tertiary"
        @click="exitFullScreen"
    />

    <IconButton
        v-if="!isFullScreen"
        icon="maximaze"
        :title="t('ui.full-screen-toggle.enter')"
        :class="{ invert: props.invert }"
        class="size-m tertiary"
        @click="enterFullScreen"
    />
</template>


<script setup>
    import IconButton from 'ðŸ”—/components/ui/IconButton.vue';
    import { useI18n } from 'vue-i18n';
    import { ref, watch, onMounted, onUnmounted } from 'vue';

    const { t } = useI18n();

    const props = defineProps({
        target: { type: String, default: 'body' },
        invert: { type: Boolean, default: false }
    });

    const isFullScreen = ref(false);
    let targetElement = null;

    const getTargetElement = () => {
        if (props.target === 'body') {
            return document.body;
        } else {
            return document.querySelector(props.target);
        }
    }

    const enterFullScreen = () => {
        targetElement = getTargetElement();

        if (!targetElement) {
            return;
        }

        if (targetElement.requestFullscreen) {
            targetElement.requestFullscreen();
        } else if (targetElement.webkitRequestFullscreen) {
            targetElement.webkitRequestFullscreen();
        } else if (targetElement.mozRequestFullScreen) {
            targetElement.mozRequestFullScreen();
        } else if (targetElement.msRequestFullscreen) {
            targetElement.msRequestFullscreen();
        }
    }

    const exitFullScreen = () => {
        if (document.fullscreenElement) {
            document.exitFullscreen();
        } else if (document.webkitFullscreenElement) {
            document.webkitExitFullscreen();
        } else if (document.mozFullScreenElement) {
            document.mozCancelFullScreen();
        } else if (document.msFullscreenElement) {
            document.msExitFullscreen();
        }
    }

    const onFullScreenChange = () => {
        isFullScreen.value = !!document.fullscreenElement;
    }

    const onKeyDown = (e) => {
        if (e.key === 'Escape' && isFullScreen.value) {
            exitFullScreen();
        }
    }

    onMounted(() => {
        document.addEventListener('fullscreenchange', onFullScreenChange);
        window.addEventListener('keydown', onKeyDown);
    });

    onUnmounted(() => {
        document.removeEventListener('fullscreenchange', onFullScreenChange);
        window.removeEventListener('keydown', onKeyDown);
    });
</script>
