<template>
    <div class="items-editor">
        <PageHeader :title="t(props.t.title)" :detail="!!data.item" :id="`items-editor-head-buttons-${data.code}`">
            <template #title>
                <IconButton
                    icon="arrow-left"
                    @click="emit('close')"
                    class="size-l tertiary rtl-sensitive"
                    :name="t(props.t.back)"
                />

                <slot name="page-header-title" /> 
            </template>

            <template #default>
                <template v-if="data.viewMode === 'table'">
                    <Button :name="t(`ui.items-editor.save-all`)"
                        class="size-s secondary"
                        :disabled="saveAllActions.length < 1"
                        @click="saveAll"
                    />
                </template>


                <KebabMenu
                    icon="plus"
                    v-if="props.canAdd"
                    :leftAuto="!locale.rtl"
                    :rightAuto="locale.rtl"
                    :tertiary="true"
                    :invert="!!data.item"
                    size="size-m"
                >
                    <ul>
                        <li @click="addOne">
                            <Icon icon="plus" />
                            <span>{{ t(props.t['add-one']) }}</span>
                        </li>

                        <li @click="selectItems" v-if="props.selectItems">
                            <Icon icon="folder" />
                            <span>{{ t(props.t['select-several']) }}</span>
                        </li>
                    </ul>
                </KebabMenu>

                <FullScreenToggle :invert="!!data.item" />

                <ViewMode :code="props.code"
                    :invert="!!data.item"
                    :disabled="data.hasChanges?.length > 1"
                    :modes="['list', 'table']"
                    @change="setViewMode"
                />

                <!-- <Button :name="'Save all'"
                    class="size-s secondary"
                    :disabled="true"
                    @click=""
                    v-if="data.viewMode === 'table'"
                /> -->
            </template>
        </PageHeader>

        <div class="box" :class="{ table: data.viewMode === 'table' }" v-drag-scroll>
            <ItemsEditorHeader v-if="data.viewMode === 'table' && data.sections?.length && data.items.length" :sections="data.sections" />


            <template v-if="data.viewMode === 'list'">
                <slot name="items"
                    :id="data.item?.id || data.item?.localId"
                    :items="data.items"
                    :change="setItem"
                    :sortable="props.sortable"
                    :sort="onItemsSort"
                />
            </template>


            <draggable
                v-if="data.items.length"
                v-model="data.items"
                :item-key="(item) => item.id || item.localId"
                handle=".sort-button"
                ghost-class="dragging"
                @end="onItemsSort({
                    oldIndex: $event.oldIndex,
                    newIndex: $event.newIndex
                })"
                class="item-detail-box"
            >
                <template #item="{ element: item, index: position }">
                    <div v-if="data.viewMode === 'table' || (item.id && data.item?.id === item.id) || (data.item?.localId === item.localId && item.localId)"
                        class="item-detail"
                    >
                        <template v-if="data.viewMode === 'table'">
                            <div class="item-preview">
                                <Button icon="drag"
                                    v-if="props.sortable"
                                    class="tertiary submenu size-m sort-button" />

                                <slot name="preview"
                                    :item="item"
                                />
                            </div>
                        </template>

                        <slot name="item"
                            :toolbarId="`items-editor-head-buttons-${data.code}`"
                            :position="position + 1"
                            :item="item"
                            :viewMode="data.viewMode"
                            :edit="onEdit"
                            :add="onAdd"
                            :update="onUpdate"
                            :remove="onRemove"
                            :close="onClose"
                            :hide="onHide"
                            :sections="setSections"
                            :actions="setActions"

                        />
                    </div>
                </template>
            </draggable>

            <EmptyLabel
                v-if="data.viewMode === 'table' && !data.items.length"
                class="empty"
                :title="t(props.t.empty.title)"
                :description="t(props.t.empty.description)"
            />
        </div>
    </div>
</template>

<script setup>
    import { reactive, computed, onMounted } from 'vue';
    import draggable from 'vuedraggable'

    import IconButton from 'ðŸ”—/components/ui/IconButton.vue';
    import PageHeader from '@/components/blocks/PageHeader/PageHeader.vue';

    import ItemsEditorHeader from './ItemsEditorHeader.vue';
    import KebabMenu from 'ðŸ”—/components/ui/KebabMenu.vue';
    import FullScreenToggle from 'ðŸ”—/components/ui/FullScreenToggle.vue'
    import ViewMode from 'ðŸ”—/components/ui/ViewMode.vue';
    import EmptyLabel from 'ðŸ”—/components/ui/EmptyLabel.vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';

    import { useI18n } from 'vue-i18n';
    import { code, delay } from 'ðŸ”—/utils/utils';

    import api from 'ðŸ”—/api';
    import stores from '@/stores';

    const emit = defineEmits(['update', 'add', 'remove', 'close', 'sort']);

    const modals = stores.modals();
    const user = stores.user();
    const locale = stores.locale();

    const { t } = useI18n();

    const props = defineProps({
        t: { type: [Object, null], default: null },
        code: { type: String },
        canAdd: { type: Boolean, default: false },
        sortable: { type: Boolean, default: false },
        getItem: { type: Function, default: () => {} },
        selectItems: { type: Function, default: () => {} },
        items: { type: [Array, null], default: null }
    });

    const data = reactive({
        code: code(),
        item: null,
        items: props.items?.length ? JSON.parse(JSON.stringify(props.items)) : [],
        sections: [],
        viewMode: localStorage[`view-mode-${props.code}`] || 'list',
        actions: {}
    });

    const setViewMode = (mode) => {
        if (data.hasChanges?.length && (data.item?.localId !== data.hasChanges[0]) && (data.item?.id !== data.hasChanges[0])) {
            data.item = data.items.find(item => (item.id && item.id === data.hasChanges[0])
                || item.localId === data.hasChanges[0]);
        }

        data.viewMode = mode;
    }

    const setSections = (payload) => {
        if (!data.sections?.length) {
            data.sections = payload || [];
        }
    }

    // Actions
        const setActions = (payload) => {
            console.log(payload.item.id, payload.item.localId, '>>>>')
            if (!payload?.item?.id && !payload?.item?.localId) {
                return;
            }

            if (!!payload?.actions?.length) {
                data.actions[payload?.item?.id || payload?.item?.localId] = payload.actions;
            } else {
                delete data.actions[payload?.item?.id || payload?.item?.localId];
            }
        }

        const clearActions = (payload) => {
            if (!payload?.id && !payload?.localId) {
                return;
            }

            delete data.actions[payload?.id || payload?.localId];
        }

        // Save all
        const saveAllActions = computed(() => {
            const result = [];
            Object.values(data.actions).forEach(item =>
                result.splice(0, 0, ...(item?.filter(action => action.type === 'save' || action.type === 'create') || []))
            );

            return result;
        });

        const saveAll = async () => {
            await Promise.all(saveAllActions.value.filter(item => !!item.action).map(async (item) => {
                await item.action();
            }));

            // await delay(200);
        }

    const onEdit = (payload) => {
        if (!payload?.id && !payload?.localId) {
            return;
        }

        if (payload?.title) {
            if (!data.hasChanges) {
                data.hasChanges = []
            }
            if (data.hasChanges.indexOf(payload?.id || payload?.localId) === -1) {
                data.hasChanges.push(payload?.id || payload?.localId);
            }
        }

        const item = data.items.find(item => (item.id === payload.id && payload.id)
            || (item.localId === payload.localId && payload.localId));

        if (item) {
            data.items.splice(data.items.indexOf(item), 1, payload);
        } else {
            data.items.push(payload);
        }
    }

    const clearChanges = (id, localId) => {
        if (data.hasChanges?.length) {
            if (id && data.hasChanges.indexOf(id) !== -1) {
                data.hasChanges.splice(data.hasChanges.indexOf(id), 1);
            }

            if (localId && data.hasChanges.indexOf(localId) !== -1) {
                data.hasChanges.splice(data.hasChanges.indexOf(localId), 1);
            }

            if (!data.hasChanges?.length) {
                delete data.hasChanges;
            }
        }
    }

    const onUpdate = (payload, mode) => {
        if (!payload?.id) {
            return;
        }

        const item = data.items.find(item => item.id === payload.id);
        if (item) {
            data.items.splice(data.items.indexOf(item), 1, payload);
        }

        emit('update', payload);

        if (mode !== 'not-clear-changes') {
            clearChanges(payload?.id, payload?.localId);
        }

        if (mode === 'add-one') {
            addOne();
        }
    }

    const onAdd = (payload, mode) => {
        if (!payload?.id && !payload?.localId) {
            return;
        }

        const item = data.items.find(item => (item.id === payload.id && payload.id)
            || (item.localId === payload.localId && payload.localId));

        if (payload.id && data.item.localId === payload.localId) {
            data.item.id = payload.id;
        }

        if (item) {
            data.items.splice(data.items.indexOf(item), 1, payload);
        } else {
            data.items.push(payload);
        }

        emit('add', payload);

        clearChanges(payload?.id, payload?.localId);

        if (mode === 'add-one') {
            addOne();
        }
    }

    const onRemove = (id) => {
        if (!id) {
            return;
        }

        const item = data.items.find(item => item.id === id);
        if (item) {
            data.items.splice(data.items.indexOf(item), 1);
        }

        emit('remove', id);

        if (data.item?.id === id) {
            data.item = null;
        }

        clearActions({ id });
        clearChanges(id);
    }

    const create = (count) => {
        let i = 1;
        while (i <= Math.max(count, 1)) {
            data.items.push({ localId: code() })
            i += 1;
        }

        setItem(data.items[data.items.length - Math.max(count, 1)]);
    }

    const add = (payload) => {
        if (data.items.some(item => item.id === payload.id)) {
            return;
        }

        data.items.push(payload);
    }

    const addOne = () => {
        const doIt = () => {
            const currentIndex = data.items.indexOf(data.item);
            const next = data.items.find((item, id) => !item.id && id > currentIndex );

            if (next) {
                setItem(next);
            } else {
                create(1);
            }
        }

        if (data.viewMode === 'list') {
            checkUnsaved(doIt);
        } else {
            doIt();
        }
    }

    const selectItems = () => {
        if (!props.selectItems) {
            return;
        }

        props.selectItems(data.items);
    }

    const onClose = (payload) => {
        if (!payload?.id && !payload.localId) {
            return;
        }

        const doIt = async () => {
            clearChanges(payload?.id, payload?.localId);
            clearActions(payload);

            const actualItem = payload?.id ? await props.getItem(payload.id) : { localId: payload?.localId 
            }

            const index = data.items.findIndex(item => (payload?.id && item.id === payload?.id) || (item.localId === payload?.localId));

            // ÐžÑ‚Ð¼ÐµÐ½ÑÐµÐ¼ Ð¸Ð·Ð¼ÐµÐ½ÐµÐ½Ð¸Ñ
            if (index !== -1) {
                // data.items.splice(index, 1);
                data.items.splice(index, 1, actualItem);
            }

            if (payload?.id && data.item?.id === payload?.id || data.item?.localId === payload?.localId) {
                data.item = null;
            }
        }

        if (data.hasChanges && (data.hasChanges.indexOf(payload?.id) !== -1 || data.hasChanges.indexOf(payload?.localId) !== -1)) {
            modals.confirm({
                title: t(props.t.revert.confirm.title),
                body: t(props.t.revert.confirm.description, {
                    title: data.item.title
                }),
                onOk: () => {
                    doIt();
                }
            }); 
            return;
        } else {
            doIt();
        }
    }

    const onHide = (payload) => {
        if (!payload?.id && !payload.localId) {
            return;
        }

        clearChanges(payload?.id, payload?.localId);
        clearActions(payload);

        const index = data.items.findIndex(item => (payload?.id && item.id === payload?.id) || (item.localId === payload?.localId));

        // ÐžÑ‚Ð¼ÐµÐ½ÑÐµÐ¼ Ð¸Ð·Ð¼ÐµÐ½ÐµÐ½Ð¸Ñ
        if (index !== -1) {
            data.items.splice(index, 1);
        }

        if (payload?.id && data.item?.id === payload?.id || data.item?.localId === payload?.localId) {
            data.item = null;
        }
    }

    const checkUnsaved = async (callback) => {
        if (data.hasChanges) {
            modals.confirm({
                title: t(props.t.unsaved.confirm.title),
                body: t(props.t.unsaved.confirm.description),
                onOk: async() => {
                    if (data.item) {
                        clearChanges(data.item?.id, data.item?.localId);
                        clearActions(data.item);

                        const payload = data.item?.id ? await props.getItem(data.item.id) : { localId: data.item?.localId 
                        }

                        const index = data.items.findIndex(item => (data.item?.id && item.id === data.item?.id) || (item.localId === data.item?.localId));

                        // ÐžÑ‚Ð¼ÐµÐ½ÑÐµÐ¼ Ð¸Ð·Ð¼ÐµÐ½ÐµÐ½Ð¸Ñ
                        if (index !== -1) {
                            data.items.splice(index, 1, payload);
                        }
                    }

                    // delete data.hasChanges;

                    callback();
                }
            }); 
            return;
        }

        callback();
    }

    const setItem = (item) => {
        checkUnsaved(() => {
            data.item = null;

            setTimeout(() => {
                data.item = item;
            });
        });
    }

    const onItemsSort = ({ oldIndex, newIndex }) => {
        emit('sort', {
            // items: data.items.slice(Math.min(newIndex, oldIndex), Math.max(newIndex, oldIndex) + 1),
            // fromIndex: Math.min(oldIndex, newIndex) + 1,
            newIndex,
            oldIndex,
            items: data.items
        })
    }

    onMounted(() => {
        if (data.items?.length) {
            data.item = data.items[0];
        }
    });

    defineExpose({
        create,
        add
    });
</script>

<style lang="scss" scoped>
    .items-editor {
        display: flex;
        flex: 1 1 100%;
        width: 100%;
        height: 100%;
        flex-direction: column;
        gap: 16px;
        &:fullscreen {
            background-color: var(--white-500);
        }
    }

    @include studio.default-box;

    .chevron-down {
        fill: var(--white-500);
        stroke: none;
        transition: all .4s;

        &.opened {
            transform: rotate(180deg);
        }
    }

    .title {
        font: var(--font-h4);
        font-weight: bold;
        color: var(--dark-500);
    }

    .box {
        .item-detail-box {
            flex: 1 1 100%;
            display: flex;
            flex-direction: column;
        }

        &.table {
            overflow: auto;
            @include scrollbar();
            flex-direction: column;
            gap: 0;
            padding: 0;

            .item-detail {
                padding: 0;
                display: flex;
                flex-direction: row;
                gap: 0;
                overflow: visible;
                width: max-content;
                border-bottom: solid 1px var(--dark-10);
                min-width: 100%;
                // min-height: 132px;
                height: auto;
                min-height: auto;

                .item-preview {
                    width: 324px;
                    min-width: 324px;
                    max-width: 324px;
                    flex-shrink: 0;
                    position: sticky;
                    left: 0;
                    z-index: 1;
                    background: var(--white-500);
                    padding: 16px 8px 16px 48px;
                    box-shadow: 8px 0px 8px -12px var(--dark-500);
                    display: flex;
                    flex-direction: row;
                    gap: 8px;

                    .sort-button {
                        margin-left: -30px;
                        cursor: grab;
                    }
                }
                &:nth-child(2n) {
                }
            }
        }
    }
    .direction-rtl .box {
        &.table {
            .item-detail {
                .item-preview {
                    left: auto;
                    right: 0;
                    box-shadow: -8px 0px 8px -12px var(--dark-500);
                }
            }
        }
    }
</style>
