<template>
    <AutoComplete
        v-bind="props"

        :optionList="data.optionList || []"
        @search="onAutoCompleteSearch"
        @change="emit('change', $event)"
    >
        <template #option="{ option }">
            <span class="artist-option-preview">
                <Img v-if="option.profileImage?.resized || defaultProfileImage?.resized"
                    :preview="option.profileImage?.resized || defaultProfileImage?.resized"
                    :original="option.profileImage?.original || defaultProfileImage?.original"
                    :alt="option?.name"
                />
                <span class="name">{{ option.name }}</span>
            </span>
        </template>
        <template #value="{ value }">
            <span class="artist-value-preview">
                <Img v-if="value.profileImage?.resized || defaultProfileImage?.resized"
                    :preview="value.profileImage?.resized || defaultProfileImage?.resized"
                    :original="value.profileImage?.original || defaultProfileImage?.original"
                    :alt="value?.name"
                />
                <span>
                    <span class="name">{{ value.name }}</span>
                </span>
            </span>
        </template>
    </AutoComplete>
</template>

<script setup>
    import { reactive, onMounted, computed } from 'vue';
    import AutoComplete from 'ðŸ”—/components/ui/AutoComplete.vue';
    import Img from 'ðŸ”—/components/ui/Img.vue';
    import api from 'ðŸ”—/api';
    import { code } from 'ðŸ”—/utils/utils';

    const emit = defineEmits(['change']);

    const props = defineProps({
    });

    const data = reactive({
        code: code(),
        optionList: []
    });

    const defaultProfileImage = computed(() => {
        return api.artists().defaultProfileImage;
    });

    const onAutoCompleteSearch = async (value) => {
        const searchStr = value?.toLowerCase().trim();

        const params = {
            skip: 0,
            take: 20,
            sort: 'name',
            sort_dir: 'asc'
        }

        if (searchStr?.trim()) {
            params.name = searchStr.trim().toLowerCase();
        }

        const payload = await api.artists().list(params, data.code);

        if (!payload?.items) {
            data.optionList = [];
            return;
        }

        data.optionList = await payload.items.map(item => ({
            id: item.id,
            code: item.id,
            name: item.name,
            profileImage: item.profileImage
        }));
    }

    onMounted(() => {
        onAutoCompleteSearch();
    })
</script>

<style lang="scss" scoped>
    .artist-option-preview {
        @include artist-option-preview;
    }

    .artist-value-preview {
        @include artist-value-preview;
    }
</style>
