<template>
    <div class="form-line"
        v-if="props.field || props.help || !!slots.action || !!slots.title"
        :class="{
            column: props.column,
            'view-mode-table': props.viewMode === 'table'
        }"
        :style="style"
    >
        <slot name="title">
            <label v-if="props.field?.name && props.viewMode === 'list'">
                <span>{{ props.field.name }}</span>
                <span class="attention" v-if="props.field.attention">
                    {{ props.field.attention }}
                </span>
            </label>
        </slot>

        <div class="box">
            <slot />

            <span class="attention" v-if="hasAttentions">
                <template v-if="props.help?.length && props.viewMode === 'list'">
                    <template v-for="item in props.help" :key="item.title">
                        <span v-if="item.title">
                            <b>{{ item.title }}</b>
                        </span>
                        <span>
                            {{ item.description }}
                        </span>
                    </template>
                </template>
                <template v-if="props.field?.attentions?.length && props.viewMode === 'list'">
                    <span v-for="item in props.field.attentions" :key="item">{{ item }}</span>
                </template>
                <slot name="action"></slot>
            </span>
        </div>
    </div>
</template>

<script setup>
    import { useSlots, computed } from 'vue'

    const slots = useSlots();

    const props = defineProps({
        field: { type: [Object, null], default: null },
        help: { type: [Object, null], default: null },
        column: { type: Boolean, default: false },
        viewMode: { type: String, default: 'list' }
    });

    const style = computed(() => {
        if (props.viewMode === 'list') {
            return {};
        }

        return {
            width: (props.field?.width || 200) + 'px',
            'max-width': (props.field?.width || 200) + 'px',
            'min-width': (props.field?.width || 200) + 'px'
        }
    });

    const hasAttentions = computed(() => {
        return (props.viewMode === 'list' && (props.field?.attentions?.length || props.help?.length)) || !!slots.action
    });
</script>

<style lang="scss" scoped>
</style>
